<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/clinicaltrial">临床试验</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="remove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建时间">
            <Input v-model="editForm.CreateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最新更新时间">
              <Input v-model="editForm.LatestUpdateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验编号" prop="NCTID">
              <Input v-model="editForm.NCTID" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验名称" prop="Title">
              <Input v-model="editForm.Title" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验分期" prop="Phase">
            <Input v-model="editForm.Phase" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验状态" prop="Status">
              <Input v-model="editForm.Status" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/targetedtherapymodal/updateListClinicalTrial.php",qs.stringify({
        ClinicalTrialID:this.clinicalTrialID
      })).then(response=>{
        // console.log(response.data);
        this.editForm = response.data.list[0]
      })
    },
    data() {
      return {
        clinicalTrialID:location.search.split("=")[1],
        editForm:{},
        rule:{
          NCTID:[
            {
              required:true,message:'不能为空',trigger:'blue,change'
            }
          ],
          Title:[
            {
              required:true,message:'不能为空',trigger:'blue,change'
            }
          ],
          Phase:[
            {
              required:true,message:'不能为空',trigger:'blue,change'
            }
          ],
          Status:[
            {
              required:true,message:'不能为空',trigger:'blue,change'
            }
          ]
        }
      }
    },
    methods: {
      remove(){
        // console.log(id);
        this.$http.post("/php/targetedtherapymodal/delClinicalTrial.php",qs.stringify({
          ClinicalTrialID:[this.clinicalTrialID]
        })).then(response=>{
            // console.log(response.data);
            if(response.data){
              this.$Message.success("删除成功！即将跳转页面...")
              setTimeout(()=>{
                this.$router.push("/targetedtherapymodal/clinicaltrial")
              },1000)
            }else{
              this.$Message.error("删除失败！")
            }
          })
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {};
            for(var key in this.editForm){
              obj[key] = this.editForm[key].trim()
            }
            this.$http.post("/php/targetedtherapymodal/editClinicalTrial.php",qs.stringify(obj)).then(response=>{
              // console.log(response.data);
              if(response.data){
                this.$Message.success('保存成功，即将跳转页面...');
                setTimeout(()=>{
                  this.$router.push("/targetedtherapymodal/clinicaltrial")
                },1000)
              }else{
                this.$Message.error('保存失败,请检查输入内容');
              }
            })
          }
        })
      },
      backList(){
        this.$router.push("/targetedtherapymodal/clinicaltrial")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


