<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/gene">基因管理</BreadcrumbItem>
      <BreadcrumbItem>添加新的基因</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的基因</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的基因</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="Gene Symbol" prop="Symbol">
              <Input v-model="addForm.Symbol" autofocus placeholder="请输入Gene Symbol" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="基因名称" prop="GeneName">
              <Input v-model="addForm.GeneName" placeholder="请输入基因名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体名称" prop="ChrName">
            <Input v-model="addForm.ChrName" placeholder="请输入染色体名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="物理图谱位置">
              <Input v-model="addForm.Location" placeholder="请输入物理图谱位置" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="英文功能描述">
            <Input v-model="addForm.Synonyms" type="textarea" :rows="4" placeholder="请输入英文功能描述" style="width: 280px"></Input>
            <Button type="dashed" shape="circle" style="width:120px">添加至分配列表</Button>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
import { sendChrID, reciveChrID } from '../../../assets/switchChrName2ID.js'
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          Symbol:'',
          GeneName:'',
          ChrName:'',
          Location:'',
          Synonyms:''
        },
        rule:{
          Symbol:[
            {
              required: true, message: 'Gene Symbol不能为空', trigger: 'blur,change'
            }
          ],
          GeneName:[
            {
              required: true, message: '基因名称不能为空', trigger: 'blur,change'
            }
          ],
          ChrName:[
            {
              required: true, message: '染色体名称不能为空', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = this.addForm;
            obj.ChrID = sendChrID(obj.ChrName)
            if(obj.ChrID!='error'){
              this.$http.post("/php/gene/add.php",qs.stringify(obj)).then(response=>{
                console.log(response.data);
                if(response.data==='wrong'){
                  this.$Message.error('该基因已存在！请检查输入内容');
                  return false;
                }
                if(response.data){
                  this.$Message.success('添加成功，即将跳转页面...');
                  setTimeout(()=>{
                  this.$router.push('/basicdatamodal/gene');      
                  },1000)
                }else{
                  this.$Message.error('添加失败，请检查输入的内容');
                }
              })
            }else{
              this.$Message.error('染色体名称错误，请重新输入');
            }
          }
        })
      },
      backList(){
        this.$router.push("/basicdatamodal/gene")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


