<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/targetedtherapy2">靶向治疗疗法</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="remove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建时间">
            <Input v-model="editForm.CreateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最新更新时间">
              <Input v-model="editForm.LatestUpdateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="靶向治疗疗法名称" prop="TherapyName">
            <Input v-model="editForm.TherapyName" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/targetedtherapy2/edit.php",qs.stringify({
        TargetTreatmentTherapyID:this.targetTreatmentTherapyID
      })).then(response=>{
        // console.log(response.data);
        this.editForm = response.data[0]
      })
    },
    data() {
      return {
        targetTreatmentTherapyID:location.search.split('=')[1],
        editForm:{
          TherapyName:'',
          CreateDate:'',
          LatestUpdateDate:''
        },
        rule:{
          TherapyName:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      remove(){
        // console.log(id);
        this.$http.post("/php/targetedtherapy2/del.php",qs.stringify({
          TargetTreatmentTherapyID:[this.targetTreatmentTherapyID]
        })).then(response=>{
          // console.log(response.data);
          if(response.data){
            if(response.data){
              this.$Message.success('删除成功！正在刷新页面...');
              setTimeout(()=>{
                this.$router.push("/targetedtherapymodal/targetedtherapy2")
              },1000)
            }else{
              this.$Message.error('删除失败！');
            }
          }
        }) 
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj={};
            obj.TargetTreatmentTherapyID = this.targetTreatmentTherapyID;
            obj.TherapyName = this.editForm.TherapyName.trim();
            this.$http.post("/php/targetedtherapy2/update.php",qs.stringify(obj)).then(response=>{
              console.log(response.data);
              if(response.data==='wrong'){
                this.$Message.warning('该疗法名称已存在，请重新输入');
                return false;
              }
              if(response.data){
                this.$Message.success('保存成功！页面正在跳转...');
                setTimeout(()=>{
                  this.$router.push("/targetedtherapymodal/targetedtherapy2")
                },1000)
              }else{
                this.$Message.error('保存失败！请检查输入内容');
              }
            })
          } else {
            this.$Message.error('Fail!');
          }
        })
      },
      backList(){
        this.$router.push("/targetedtherapymodal/targetedtherapy2")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


