<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/gene">基因管理</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <!--<Button type="warning">删除</Button>-->
      </div>
      <div class="browContainer">
        <Form ref="browForm" :model="browForm" label-position="right" :label-width="100">
          <FormItem label="Gene Symbol">
            <Input v-model="browForm.Symbol" readonly style="width: 280px"></Input>
            <Button type="dashed" shape="circle" @click="showTranscript(geneID)" style="width:120px">显示转录本信息</Button>
          </FormItem>
          <FormItem label="基因名称">
              <Input v-model="browForm.GeneName" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体名称">
            <Input v-model="browForm.ChrName" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="物理图谱位置">
              <Input v-model="browForm.Location" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="英文功能描述">
              <Input v-model="browForm.Synonyms" type="textarea" readonly :rows="4" placeholder="请输入英文功能描述" style="width: 280px"></Input>
              <Button type="dashed" shape="circle" style="width:120px">添加至分配列表</Button>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="editSubmit">编辑</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
      <div class="relatedContainer">
        <div class="deleteContainer">
          <Button type="warning">关联新文献</Button>
        </div>
        <Table stripe border ref="selection" height="200"  :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/gene/",qs.stringify({GeneID:this.geneID})).then(response=>{
        // console.log(response.data);
        this.browForm = response.data.data[0];
        if(Number(this.browForm.ChrID)<23){
          this.browForm.ChrName = "chr" + this.browForm.ChrID
        }else if(this.browForm.ChrID==='23'){
          this.browForm.ChrName = "chrX"
        }else if(this.browForm.ChrID==='24'){
          this.browForm.ChrName = "chrY"
        }else if(this.browForm.ChrID==='25'){
          this.browForm.ChrName = "chrM"
        }
      })
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        geneID:location.search.split("=")[1],
        browForm:{},
        multipleSelection:[],
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"Gene Symbol",
            key:'geneSymbol',
            align: 'center'
          },
          {
            title:"基因名称",
            key:'geneName',
            align: 'center'
          },
          {
            title:"染色体名称",
            key:'ChrName',
            align: 'center'
          },
          {
            title:"物理图谱位置",
            key:'physicalMap',
            align: 'center'
          },
          {
            title:"功能描述",
            key:'functionDesc',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'createData',
            align: 'center'
          },
          {
            title:"最后修改日期",
            key:'lastUpdatadata',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.index)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            geneSymbol:"11111",
            geneName:"8",
            ChrName:"1.2.0",
            physicalMap:"11111",
            functionDesc:"你好",
            createData:"2018-03-06",
            lastUpdatadata:"2018-03-06"
          },
          {
            geneSymbol:"11111",
            geneName:"8",
            ChrName:"1.2.0",
            physicalMap:"11111",
            functionDesc:"你好",
            createData:"2018-03-06",
            lastUpdatadata:"2018-03-06"
          },
          {
            geneSymbol:"11111",
            geneName:"8",
            ChrName:"1.2.0",
            physicalMap:"11111",
            functionDesc:"你好",
            createData:"2018-03-06",
            lastUpdatadata:"2018-03-06"
          },
          {
            geneSymbol:"11111",
            geneName:"8",
            ChrName:"1.2.0",
            physicalMap:"11111",
            functionDesc:"你好",
            createData:"2018-03-06",
            lastUpdatadata:"2018-03-06"
          }
        ],
      }
    },
    methods: {
      showTranscript(id){
        // console.log(id);
        this.$router.push('/basicdatamodal/transcriptlist?geneid=' + id)
      },
      editSubmit() {
        this.$router.push('/basicdatamodal/editgenedetail?geneid=' + this.geneID)
      },
      backList(){
        this.$router.push("/basicdatamodal/gene")
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .browContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relatedContainer{
    border-top:1px dotted #ccc;
    .batchContainer{
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
  }
}
</style>


