<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>靶向治疗</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="临床试验编号">
            <Input v-model="searchForm.NCTID" placeholder="请输入分子谱名称"></Input>
          </FormItem>
          <FormItem label="分子谱名称">
            <Input v-model="searchForm.MolProfileName" placeholder="请输入分子谱名称"></Input>
          </FormItem>
          <FormItem label="疾病名称">
            <Input v-model="searchForm.DiseaseName_cn" placeholder="请输入疾病信息"></Input>
          </FormItem>
          <FormItem label="Response Type">
            <Select v-model="searchForm.ResponseType" style="width:162px">
              <Option value="1">1</Option>
              <Option value="2">2</Option>
              <Option value="3">3</Option>
            </Select>
          </FormItem>
          <FormItem label="治疗疗法名称">
            <Input v-model="searchForm.TherapyName" placeholder="请输入治疗疗法"></Input>
          </FormItem>
          <FormItem label="证据类型">
            <Select v-model="searchForm.EvidenceType" style="width:162px">
              <Option value="1">1</Option>
              <Option value="2">2</Option>
              <Option value="3">3</Option>
            </Select>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <!--<div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addNewTargetedTherapy">添加新靶向治疗</Button>
      </div>-->
      <div class="geneListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/targetedtherapy/list.php").then(response=>{
        // console.log(response.data);
        this.tableData = response.data.list
        this.totalNum = Number(response.data.total)
        this.loading=false;
      })
    },
    data() {
      return {
        searchForm:{
          ClinicalTrialID:'',
          MolProfileName:'',
          DiseaseName_cn:'',
          ResponseType:'',
          TherapyName:'',
          EvidenceType:''
        },
        loading:true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"临床试验编号",
            key:'NCTID',
            align: 'center'
          },
          {
            title:"分子谱名称",
            key:'MolProfileName',
            align: 'center'
          },
          {
            title:"疾病名称",
            key:'DiseaseName_cn',
            align: 'center'
          },
          {
            title:"Response Type",
            key:'ResponseType',
            align: 'center'
          },
          {
            title:"治疗疗法名称",
            key:'TherapyName',
            align: 'center'
          },
          {
            title:"证据等级",
            key:'EvidenceType',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后更新日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            width:150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.TargetTreatmentID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.TargetTreatmentID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.TargetTreatmentID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        totalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.pageSize = 10;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      },
      addNewTargetedTherapy(){
        this.$router.push("/targetedtherapymodal/addnewtargetedtherapy")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/targetedtherapymodal/browtargetedtherapydetail?targettreatmentid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/targetedtherapymodal/edittargetedtherapydetail?targettreatmentid=" + id)
      },
      remove(id){
        // console.log(id);        
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        let obj = {}
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
        }
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        this.$http.post("/php/targetedtherapy/list.php",qs.stringify(obj)).then(response=>{
          // console.log(response.data);
          this.tableData = response.data.list
          this.totalNum = Number(response.data.total)
          this.loading=false;
        })
        
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .geneListcontainer{
    .tableColumnDisplay{
      display:hidden;
    }
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
    
  }
  
}
</style>


