<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>临床试验</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="临床试验编号">
            <Input v-model="searchForm.NCTID" placeholder="请输入临床试验编号"></Input>
          </FormItem>
          <FormItem label="临床试验名称">
            <Input v-model="searchForm.Title" placeholder="请输入临床试验名称"></Input>
          </FormItem>
          <FormItem label="临床试验分期">
            <Input v-model="searchForm.Phase" placeholder="请输入临床试验状态"></Input>
          </FormItem>
          <FormItem label="临床试验状态">
            <Input v-model="searchForm.Status" placeholder="请输入临床试验状态"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addNewClinicalTrial">添加新临床试验</Button>
      </div>
      <div class="geneListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" @click="batchRemove" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/targetedtherapymodal/listClinicalTrial.php").then(response=>{
        // console.log(response.data);
        this.tableData = response.data.list;
        this.totalNum = Number(response.data.total);
        this.loading = false;
      })
    },
    data() {
      return {
        searchForm:{
          NCTID:'',
          Title:'',
          Phase:'',
          Status:''
        },
        loading:true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"临床试验编号",
            key:'NCTID',
            align: 'center'
          },
          {
            title:"临床试验名称",
            key:'Title',
            align: 'center'
          },
          {
            title:"临床试验分期",
            key:'Phase',
            align: 'center'
          },
          {
            title:"临床试验状态",
            key:'Status',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            width:150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.ClinicalTrialID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.ClinicalTrialID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ClinicalTrialID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        totalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.loading = true;
        this.pageSize = 10;
        this.pageNum = 1;
        let obj = {}
        let count = 0;
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
          if(obj[key]!==''){
            count++;
          }
        }
        if(count===0){
          this.$http.post("/php/targetedtherapymodal/listClinicalTrial.php").then(response=>{
            console.log(response.data);
            this.tableData = response.data.list;
            this.totalNum = Number(response.data.total);
            this.loading = false;
          })
        }else{
          obj.pageNum = this.pageNum;
          obj.pageSize = this.pageSize;
          this.$http.post("/php/targetedtherapymodal/searchClinicalTrial.php",qs.stringify(obj)).then(response=>{
            // console.log(response.data);
            this.tableData = response.data.list;
            this.totalNum = Number(response.data.total);
            this.loading = false;
          })
        }
      },
      addNewClinicalTrial(){
        this.$router.push("/targetedtherapymodal/addnewClinicalTrial")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/targetedtherapymodal/browclinicaltrialdetail?clinicaltrialid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/targetedtherapymodal/editclinicaltrialdetail?clinicaltrialid=" + id)
      },
      remove(id){
        // console.log(id);
        this.$http.post("/php/targetedtherapymodal/delClinicalTrial.php",qs.stringify({
          ClinicalTrialID:[id]
        })).then(response=>{
            // console.log(response.data);
            if(response.data){
              this.$Message.success("删除成功！正在刷新页面...")
              this.multipleSelection=[];
              //刷新页面
              this.handlePage(this.pageNum);
            }else{
              this.$Message.error("删除失败！")
            }
            this.loading = false;
          })
      },
      batchRemove(){
        // console.log(id);
        let IDS = [];
        this.multipleSelection.forEach(item=>{
          IDS.push(item.ClinicalTrialID)
        })
        this.$http.post("/php/targetedtherapymodal/delClinicalTrial.php",qs.stringify({
          ClinicalTrialID:IDS
        })).then(response=>{
            console.log(response.data);
            if(response.data){
              this.$Message.success("批量删除成功！正在刷新页面...")
              this.multipleSelection=[];
              //刷新页面
              this.handlePage(this.pageNum);
            }else{
              this.$Message.error("批量删除失败！")
            }
          })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        let obj = {}
        let count = 0;
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
          if(obj[key]!==''){
            count++;
          }
        }
        if(count===0){
          this.$http.post("/php/targetedtherapymodal/listClinicalTrial.php").then(response=>{
            // console.log(response.data);
            this.tableData = response.data.list;
            this.totalNum = Number(response.data.total);
            this.loading = false;
          })
        }else{
          obj.pageNum = this.pageNum;
          obj.pageSize = this.pageSize;
          this.$http.post("/php/targetedtherapymodal/searchClinicalTrial.php").then(response=>{
            // console.log(response.data);
            this.tableData = response.data.list;
            this.totalNum = Number(response.data.total);
            this.loading = false;
          })
        }
        
      },
      handlePageSize(val){
        this.loading = true;
        this.pageSize = val;
        this.pageNum = 1;
        let obj = {}
        let count = 0;
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
          if(obj[key]!==''){
            count++;
          }
        }
        if(count===0){
          this.$http.post("/php/targetedtherapymodal/listClinicalTrial.php").then(response=>{
            console.log(response.data);
            this.tableData = response.data.list;
            this.totalNum = Number(Number(response.data.total));
            this.loading = false;
          })
        }else{
          obj.pageNum = this.pageNum;
          obj.pageSize = this.pageSize;
          this.$http.post("/php/targetedtherapymodal/searchClinicalTrial.php").then(response=>{
            // console.log(response.data);
            this.tableData = response.data.list;
            this.totalNum = Number(response.data.total);
            this.loading = false;
          })
        }
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .geneListcontainer{
    .tableColumnDisplay{
      display:hidden;
    }
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
    
  }
  
}
</style>


