<template>
  <div class="relatedContainer">
    <div class="deleteContainer">
      <Button type="warning" @click="addNewDrug">添加新靶向药物</Button>
    </div>
    <Table stripe border :loading="loading" ref="selection" height="300"  :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
    <div class="batchContainer">
      <Button type="text" size="large" class="batchBtn" @click="batchRemove">移除选中的{{multipleSelection.length}}个条目</Button>
    </div>
    <div class="pageContainer">
      <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
    </div>
  </div>
</template>
<script>
import drugClassTpl from './drugClassTpl'
var qs = require('qs')
  export default {
    mounted() {
      this.$http.post("/php/targetedtherapy2/listTherapy.php",qs.stringify({
        TargetTreatmentTherapyID:this.therapy2
      })).then(response=>{
        // console.log(response.data);
        this.tableData = response.data.list;
        this.totalNum = Number(response.data.total);
        this.loading = false;
      })
    },
    data() {
      return {
        therapy2:location.search.split("=")[1],
        loading:true,
        multipleSelection:[],
        totalNum:0,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"药物编号",
            key:'DrugID',
            align: 'center'
          },
          {
            title:"药物英文名称",
            key:'DrugName_en',
            align: 'center'
          },
          {
            title:"药物中文名称",
            key:'DrugName_cn',
            align: 'center'
          },
          {
            title:"药物类别",
            align: 'center',
            width:200,
            render: (h, params) => {
              return h(drugClassTpl, {
                props: {
                  row: params.row
                }
              })
            }
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后修改日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.TargetTreatmentTherapyDrugID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.TargetTreatmentTherapyDrugID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.TargetTreatmentTherapyDrugID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      addNewDrug(){
        this.$router.push("/targetedtherapymodal/browtargetedtherapy2detail/addnewdrug?therapy2=" + this.therapy2)
      },
      show(id){
        this.$router.push("/targetedtherapymodal/browtargetedtherapy2detail/browdrugdetail?targettreatmenttherapydrugid=" + id)
      },
      edit(id){
        this.$router.push("/targetedtherapymodal/browtargetedtherapy2detail/editdrugdetail?targettreatmenttherapydrugid=" + id)
      },
      remove(id){
        this.loading = true;
        this.$http.post("/php/targetedtherapy2/delTherapy.php",qs.stringify({
          TargetTreatmentTherapyDrugID:[id]
        })).then(response=>{
          // console.log(response.data);
          if(response.data){
            this.$Message.success("删除成功！正在刷新页面...")
            this.multipleSelection=[]
            this.handlePage(this.pageNum);
          }else{
            this.$Message.error("删除失败！")
            this.loading = false;
          }
        })
      },
      batchRemove(){
        this.loading=true;
        let IDS=[];
        this.multipleSelection.forEach(item=>{
          IDS.push(item.TargetTreatmentTherapyDrugID)
        })
        this.$http.post("/php/targetedtherapy2/delTherapy.php",qs.stringify({
          TargetTreatmentTherapyDrugID:IDS
        })).then(response=>{
          // console.log(response.data);
          if(response.data){
            this.$Message.success("删除成功！正在刷新页面...")
            this.multipleSelection=[];
            this.handlePage(this.pageNum);
          }else{
            this.$Message.error("删除失败！")
            this.loading = false;
          }
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading=true;
        this.pageNum = val;
        this.$http.post("/php/targetedtherapy2/listTherapy.php",qs.stringify({
          TargetTreatmentTherapyID:this.therapy2,
          page:this.pageNum,
          count:this.pageSize
        })).then(response=>{
          // console.log(response.data);
          this.tableData = response.data.list;
          this.totalNum = Number(response.data.total);
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum);
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:110px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relatedContainer{
    .batchContainer{
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
</style>


