<template>
  <div class="relatedContainer">
    <div class="deleteContainer">
      <Button type="warning" @click="addNewVariant">添加新变异</Button>
    </div>
    <Table stripe border ref="selection" height="300"  :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
    <div class="batchContainer">
      <Button type="text" size="large" class="batchBtn" @click="batchRemove">移除选中的{{multipleSelection.length}}个条目</Button>
    </div>
    <div class="pageContainer">
      <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
    </div>
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let search = location.search.split("?")[1];
      if(search.indexOf("&")!==-1){
        this.molProfileID = search.split("&")[0].split("=")[1]
      }else{
        this.molProfileID=location.search.split("=")[1]
      }
      this.$http.post("/php/targetedtherapymodal/listVariant.php",qs.stringify({
        TargetTreatmentMolProfileID:this.molProfileID
      })).then(response=>{
        // console.log(response.data);
        this.tableData = response.data.list
        this.totalNum = Number(response.data.total)
      })
    },
    data() {
      return {
        molProfileID:'',
        multipleSelection:[],
        totalNum:0,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"Gene Symbol",
            key:'Symbol',
            align: 'center'
          },
          {
            title:"HGVSp",
            key:'HGVSp',
            align: 'center'
          },
          {
            title:"HGVSc",
            key:'HGVSc',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后修改日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.MolProfileVariantsID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.MolProfileVariantsID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.MolProfileVariantsID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      addNewVariant(){
        this.$router.push("/targetedtherapymodal/browvariantprofiledetail/addnewvariant?molprofileid=" + this.molProfileID)
      },
      show(id){
        this.$router.push("/targetedtherapymodal/browvariantprofiledetail/browvariantdetail?molprofileid=" + this.molProfileID + "&molprofilevariantsid=" + id)
      },
      edit(id){
        this.$router.push("/targetedtherapymodal/browvariantprofiledetail/editvariantdetail?molprofileid=" + this.molProfileID + "&molprofilevariantsid=" + id)
      },
      remove(id){
        this.loading = true;
        this.$http.post("/php/targetedtherapymodal/delVariantProfile.php",qs.stringify({
          MolProfileVariantsID:[id]
        })).then(response=>{
          // console.log(response.data);
          if(response.data){
            if(response.data){
              this.$Message.success('删除成功！正在刷新页面...');
              this.multipleSelection=[];
              this.handlePage(this.pageNum)
            }else{
              this.$Message.error('删除失败！');
              this.loading = false;
            }
          }
        })
      },
      batchRemove(){
        this.loading = true;
        let IDS=[];
        this.multipleSelection.forEach(item=>{
          IDS.push(item.MolProfileVariantsID)
        })
        this.$http.post("/php/targetedtherapymodal/delVariantProfile.php",qs.stringify({
          MolProfileVariantsID:IDS
        })).then(response=>{
          // console.log(response.data);
          if(response.data){
            if(response.data){
              this.$Message.success('删除成功！正在刷新页面...');
              this.multipleSelection=[];
              this.handlePage(this.pageNum)
            }else{
              this.$Message.error('删除失败！');
              this.loading = false;
            }
          }
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
        this.$http.post("/php/targetedtherapymodal/listVariant.php",qs.stringify({
        TargetTreatmentMolProfileID:this.molProfileID,
        page:this.pageNum,
        count:this.pageSize
      })).then(response=>{
        // console.log(response.data);
        this.tableData = response.data.list
        this.totalNum = Number(response.data.total)
      })
      },
      handlePageSize(val){
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum);
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relatedContainer{
    .batchContainer{
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
</style>


