<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/diseasepath">疾病路径</BreadcrumbItem>
      <BreadcrumbItem>添加新的疾病路径</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的疾病路径</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的疾病路径</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="子疾病编号" prop="ChildDiseaseID">
            <AutoComplete v-model="addForm.ChildDiseaseID" @on-search="searchChildDisID" @on-select="selectChildDisID" placeholder="请输入子疾病编号" style="width:280px">
              <Option v-for="(item, index) in childDiseaseArr" :value="item.DiseaseID" :key="index">{{ item.DiseaseID }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="子疾病名称（英文）">
            <AutoComplete v-model="addForm.ChildDiseaseName_en" @on-search="searchChildDisName_en" @on-select="selectChildDisName_en" placeholder="请输入子疾病名称（英文）" style="width:280px">
              <Option v-for="(item, index) in childDiseaseArr" :value="item.DiseaseName_en" :key="index">{{ item.DiseaseName_en }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="子疾病名称（中文）">
            <AutoComplete v-model="addForm.ChildDiseaseName_cn" @on-search="searchChildDisName_cn" @on-select="selectChildDisName_cn" placeholder="请输入子疾病名称（中文）" style="width:280px">
              <Option v-for="(item, index) in childDiseaseArr" :value="item.DiseaseName_cn" :key="index">{{ item.DiseaseName_cn }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="父疾病编号" prop="ParentDiseaseID">
            <AutoComplete v-model="addForm.ParentDiseaseID" @on-search="searchParentDisID" @on-select="selectParentDisID" placeholder="请输入父疾病编号" style="width:280px">
              <Option v-for="(item, index) in parentDiseaseArr" :value="item.DiseaseID" :key="index">{{ item.DiseaseID }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="父疾病名称（英文）">
            <AutoComplete v-model="addForm.ParentDiseaseName_en" @on-search="searchParentDisName_en" @on-select="selectParentDisName_en" placeholder="请输入父疾病名称（英文）" style="width:280px">
              <Option v-for="(item, index) in parentDiseaseArr" :value="item.DiseaseName_en" :key="index">{{ item.DiseaseName_en }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="父疾病名称（中文）">
            <AutoComplete v-model="addForm.ParentDiseaseName_cn" @on-search="searchParentDisName_cn" @on-select="selectParentDisName_cn" placeholder="请输入父疾病名称（中文）" style="width:280px">
              <Option v-for="(item, index) in parentDiseaseArr" :value="item.DiseaseName_cn" :key="index">{{ item.DiseaseName_cn }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/disease/index.php").then(response=>{
        // console.log(response.data);
        this.childDiseaseArr = response.data.data;
        this.parentDiseaseArr = response.data.data;
      })
    },
    data() {
      return {
        addForm: {
          ChildDiseaseID:'',
          ChildDiseaseName_en:'',
          ChildDiseaseName_cn:'',
          ParentDiseaseID:'',
          ParentDiseaseName_en:'',
          ParentDiseaseName_cn:''
        },
        childDiseaseArr:[],
        parentDiseaseArr:[],
        rule:{
          ChildDiseaseID:[{
            required:true,message:'不能为空',trigger:'blur,change'
          }],
          ParentDiseaseID:[{
            required:true,message:'不能为空',trigger:'blur,change'
          }]
        }
      }
    },
    methods: {
      searchChildDisID(value){
        this.$http.post('/php/disease/addquery.php',qs.stringify({DiseaseID:value})).then(response=>{
          // console.log(response.data);
          if(!response.data){
            this.childDiseaseArr = [];
          }else{
            this.childDiseaseArr = response.data.data;
          }
          if(this.childDiseaseArr.length===0){
            this.$Message.error('子疾病编号错误,请重新输入');
          }
        })
      },
      selectChildDisID(value){
        this.addForm.ChildDiseaseID = value;
        let arr = this.childDiseaseArr.filter(item=>{
          return item.DiseaseID === value
        })
        this.addForm.ChildDiseaseName_en = arr[0].DiseaseName_en;
        this.addForm.ChildDiseaseName_cn = arr[0].DiseaseName_cn;
      },
      searchChildDisName_en(value){
        this.$http.post('/php/disease/addquery.php',qs.stringify({DiseaseName_en:value})).then(response=>{
          // console.log(response.data);
          if(!response.data){
            this.childDiseaseArr = [];
          }else{
            this.childDiseaseArr = response.data.data;
          }
          if(this.childDiseaseArr.length===0){
            this.$Message.error('子疾病名称（英文）错误,请重新输入');
          }
        })
      },
      selectChildDisName_en(value){
        this.addForm.ChildDiseaseName_en = value;
        let arr = this.childDiseaseArr.filter(item=>{
          return item.DiseaseName_en === value
        })
        this.addForm.ChildDiseaseID = arr[0].DiseaseID;
        this.addForm.ChildDiseaseName_cn = arr[0].DiseaseName_cn;
      },
      searchChildDisName_cn(value){
        this.$http.post('/php/disease/addquery.php',qs.stringify({DiseaseName_cn:value})).then(response=>{
          // console.log(response.data);
          if(!response.data){
            this.childDiseaseArr = [];
          }else{
            this.childDiseaseArr = response.data.data;
          }
          if(this.childDiseaseArr.length===0){
            this.$Message.error('子疾病名称（英文）错误,请重新输入');
          }
        })
      },
      selectChildDisName_cn(value){
        this.addForm.ChildDiseaseName_cn = value;
        let arr = this.childDiseaseArr.filter(item=>{
          return item.DiseaseName_cn === value
        })
        this.addForm.ChildDiseaseID = arr[0].DiseaseID;
        this.addForm.ChildDiseaseName_en = arr[0].DiseaseName_en;
      },
      searchParentDisID(value){
        this.$http.post('/php/disease/addquery.php',qs.stringify({DiseaseID:value})).then(response=>{
          // console.log(response.data);
          if(!response.data){
            this.parentDiseaseArr = [];
          }else{
            this.parentDiseaseArr = response.data.data;
          }
          if(this.parentDiseaseArr.length===0){
            this.$Message.error('父疾病编号错误,请重新输入');
          }
        })
      },
      selectParentDisID(value){
        this.addForm.ParentDiseaseID = value;
        let arr = this.parentDiseaseArr.filter(item=>{
          return item.DiseaseID === value
        })
        this.addForm.ParentDiseaseName_en = arr[0].DiseaseName_en;
        this.addForm.ParentDiseaseName_cn = arr[0].DiseaseName_cn;
      },
      searchParentDisName_en(value){
        this.$http.post('/php/disease/addquery.php',qs.stringify({DiseaseName_en:value})).then(response=>{
          // console.log(response.data);
          if(!response.data){
            this.parentDiseaseArr = [];
          }else{
            this.parentDiseaseArr = response.data.data;
          }
          if(this.parentDiseaseArr.length===0){
            this.$Message.error('父疾病名称（英文）错误,请重新输入');
          }
        })
      },
      selectParentDisName_en(value){
        this.addForm.ParentDiseaseName_en = value;
        let arr = this.parentDiseaseArr.filter(item=>{
          return item.DiseaseName_en === value
        })
        this.addForm.ParentDiseaseID = arr[0].DiseaseID;
        this.addForm.ParentDiseaseName_cn = arr[0].DiseaseName_cn;
      },
      searchParentDisName_cn(value){
        this.$http.post('/php/disease/addquery.php',qs.stringify({DiseaseName_cn:value})).then(response=>{
          // console.log(response.data);
          if(!response.data){
            this.parentDiseaseArr = [];
          }else{
            this.parentDiseaseArr = response.data.data;
          }
          if(this.parentDiseaseArr.length===0){
            this.$Message.error('父疾病名称（英文）错误,请重新输入');
          }
        })
      },
      selectParentDisName_cn(value){
        this.addForm.ParentDiseaseName_cn = value;
        let arr = this.parentDiseaseArr.filter(item=>{
          return item.DiseaseName_cn === value
        })
        this.addForm.ParentDiseaseID = arr[0].DiseaseID;
        this.addForm.ParentDiseaseName_en = arr[0].DiseaseName_en;
      },
      handleSubmit(name) {
        //根据疾病编号将数据库中两个原有的疾病关联到一起
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(this.childDiseaseArr.length!==0&&this.parentDiseaseArr.length!==0){
              let obj = {};
              for(var key in this.addForm){
                obj[key] = this.addForm[key].trim();
              }
              this.$http.post("/php/disease/diseasepath/add.php",qs.stringify(obj)).then(response=>{
                // console.log(response.data);
                if(response.data==="wrong"){
                  this.$Message.error('该疾病路径已存在！请检查输入内容');
                  return false;
                }
                if(response.data){
                  this.$Message.success("保存成功！即将跳转页面...")
                  setTimeout(()=>{
                    this.$router.push('/basicdataModal/diseasepath')
                  },1000)
                }else{
                  this.$Message.error('保存失败！请检查输入的内容');
                }
              })
            }else{
              this.$Message.error('保存失败！请检查输入的内容');
            }
          }
        })
      },
      backList() {
        this.$router.push('/basicdataModal/diseasepath')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


