<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/reference">文献信息</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='browrefdetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='editrefdetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除此文献</Button>
      </div>
      <div class="editContainer">
        <Form ref="editFrom" :model="editFrom" label-position="right" :label-width="100">
          <FormItem label="作者信息">
              <Input v-model="editFrom.authorInfo" :disabled="this.currentPath==='browrefdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文献标题">
            <Input v-model="editFrom.refTitle" :disabled="this.currentPath==='browrefdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Pubmed ID">
              <Input v-model="editFrom.pubmedID" :disabled="this.currentPath==='browrefdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="杂志">
              <Input v-model="editFrom.magazine" :disabled="this.currentPath==='browrefdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Issue">
              <Input v-model="editFrom.issue" :disabled="this.currentPath==='browrefdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Volume">
              <Input v-model="editFrom.volume" :disabled="this.currentPath==='browrefdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="年份">
              <Input v-model="editFrom.publicYear" :disabled="this.currentPath==='browrefdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="页码">
              <Input v-model="editFrom.pageNum" :disabled="this.currentPath==='browrefdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit('editFrom')" v-if="this.currentPath==='browrefdetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editFrom')" v-if="this.currentPath==='editrefdetail'">保存</Button>
            <Button @click="handleSubmit('editFrom')">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        chrID:location.search.split("=")[1],
        editFrom:{
          authorInfo:'普希金',
          refTitle:'假如生活欺骗了你',
          pubmedID:'111',
          magazine:'俄罗斯',
          issue:'1111',
          volume:'11111',
          publicYear:'1888',
          pageNum:32
        }
      }
    },
    methods: {
      handleEdit(name){
        this.currentPath = 'editrefdetail'
      },
      handleSubmit(name) {
        this.$router.push('/basicdataModal/reference')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


