<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/drugclass">药物类别</BreadcrumbItem>
      <BreadcrumbItem>添加新的药物类别</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的药物类别</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的药物类别</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="药物类别（英文）" prop="DrugClassName_en">
            <Input v-model="addForm.DrugClassName_en" placeholder="请输入药物名称（英文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物类别（中文）">
              <Input v-model="addForm.DrugClassName_cn" placeholder="请输入药物名称（中文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          DrugClassName_en:'',
          DrugClassName_cn:''
        },
        rule:{
          DrugClassName_en:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {};
            for(var key in this.addForm){
              obj[key]=this.addForm[key].trim()
            }
            obj.CreateDate = new Date().toLocaleDateString();
            this.$http.post("/php/drug/drugclass/add.php",qs.stringify(obj)).then(response=>{
              if(response.data==='wrong'){
                this.$Message.error('该药物类别已存在！请检查输入内容');
                return false;
              }
              if(response.data){
                this.$Message.success("保存成功，即将跳转页面...")
                setTimeout(()=>{
                  this.$router.push('/basicdataModal/drugclass')
                },1000)
              }else{
                this.$Message.error('保存失败，请检查输入的内容');
              }
            })
          }
        })
        this.$router.push('/basicdataModal/drugclass')
      },
      backList() {
        this.$router.push('/basicdataModal/drugclass')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


