<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/targetedtherapy2">靶向治疗疗法</BreadcrumbItem>
      <BreadcrumbItem>添加新的靶向治疗疗法</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的靶向治疗疗法</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的靶向治疗疗法</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="靶向治疗疗法名称" prop="TherapyName">
            <Input v-model="addForm.TherapyName" autofocus placeholder="请输入靶向治疗疗法名称" style="width: 280px"></Input>
          </FormItem>
          <!--<FormItem label="靶向治疗疗法描述">
            <Input v-model="addForm.therapyDesc" placeholder="请输入靶向治疗疗法描述" style="width: 280px"></Input>
          </FormItem>-->
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          TherapyName:'',
          // therapyDesc:'',
        },
        rule:{
          TherapyName:[
            {
              required:true,message:"不能为空",trigger:"blur,change"
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(this.addForm.TherapyName.trim()===''){
              this.$Message.warning('疗法名称不能只由空格组成！');
              return false;
            }
            let obj={};
            obj.TherapyName = this.addForm.TherapyName.trim()
            this.$http.post('/php/targetedtherapy2/add.php',qs.stringify(obj)).then(response=>{
              console.log(response.data);
              if(response.data==='wrong'){
                this.$Message.warning('该疗法已存在，请重新输入');
                return false;
              }
              if(response.data){
                this.$Message.success('添加成功！页面正在跳转...');
                setTimeout(()=>{
                  this.$router.push("/targetedtherapymodal/targetedtherapy2")
                },1000)
              }else{
                this.$Message.error('添加失败！请检查输入内容');
              }
            })
          }
        })
      },
      backList(){
        this.$router.push("/targetedtherapymodal/targetedtherapy2")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


