<template>
  <div class="home">
    <Layout>
      <Header>
        <Col span="6">
          <img src="/static/imgs/logo_ky.png" alt="鹍远基因">
        </Col>
        <Col span="12">
          <h1>知识库管理系统</h1>
        </Col>
        <Col span="6" class="userInfo">
          <div class="avatar">
            <Button type="dashed" shape="circle" @click="backHome">返回首页</Button>
            <span @mouseenter="exitIcon" @mouseleave="userIcon" @click="showModal">
              <Avatar icon="person" size="large" />
            </span>
            <span class="username">root</span>
          </div>
        </Col>
        <Modal v-model="showExitModal" @on-ok="loginOut" width="300">
          <p style="text-align:center;font-size:16px">确认要退出吗？</p>
        </Modal>
      </Header>
      <Layout>
        <Sider hide-trigger>
          <Menu ref="sideMenu" :active-name="activeName" theme="light" :open-names="openSide" align="left">
            <div id="basicDataSider">
              <MenuItem name="1-1">
                <img src="../../static/imgs/chromoIcon.png" alt="detection" class="icon">
                <span @click="navLink('chromo')">染色体管理</span>
              </MenuItem>
              <MenuItem name="2-1">
                <img src="../../static/imgs/iconDNA.png" alt="detection" class="icon">
                <span @click="navLink('gene')">基因管理</span>
              </MenuItem>
              <Submenu name="3">
                <template slot="title">
                    <Icon type="shuffle"></Icon>
                    变异管理
                </template>
                <MenuItem name="3-1">
                  <span @click="navLink('snpindel')">SNP_InDel</span>
                </MenuItem>
                <!--<MenuItem name="3-2">
                  <span @click="navLink('cnv')">CNV</span>
                </MenuItem>
                <MenuItem name="3-3">
                  <span @click="navLink('fusion')">FUSION</span>
                </MenuItem>-->
              </Submenu>
              <Submenu name="4">
                <template slot="title">
                    <img src="../../static/imgs/diseaseIcon.png" alt="detection" class="icon">
                    疾病管理
                </template>
                <MenuItem name="4-1">
                  <span @click="navLink('disease')">疾病信息</span>
                </MenuItem>
                <MenuItem name="4-2">
                  <span @click="navLink('diseasepath')">疾病路径</span>
                </MenuItem>
              </Submenu>
              <Submenu name="5">
                <template slot="title">
                    <img src="../../static/imgs/drugIcon.png" alt="detection" class="icon">
                    药物管理
                </template>
                <MenuItem name="5-1">
                  <span @click="navLink('drug')">药物信息</span>
                </MenuItem>
                <MenuItem name="5-2">
                  <span @click="navLink('drugclass')">药物类别</span>
                </MenuItem>
              </Submenu>
              <!--<Submenu name="6">
                <template slot="title">
                    <img src="../../static/imgs/referenceIcon.png" alt="detection" class="icon">     
                    文献管理
                </template>
                <MenuItem name="6-1">
                  <span @click="navLink('reference')">文献信息</span>
                </MenuItem>
                <MenuItem name="6-2">
                  <span @click="navLink('author')">作者信息</span>
                </MenuItem>
              </Submenu>-->
            </div>
          </Menu>
        </Sider>
        <Layout :style="{padding: '0 24px'}">
          <transition name="component-fade" mode="out-in">
            <router-view></router-view>
          </transition>
          <Footer>Singlera Genomic Inc | Copyright 2018</Footer>
        </Layout>
      </Layout>
    </Layout>
  </div>
  
</template>

<script>
