          this.$refs.sideMenu.updateActiveName();
        })
      break;
      case "author":
        this.openSide = ["6"];
        this.activeName = "6-2";
        this.$nextTick(function() {
          this.$refs.sideMenu.updateOpened();
          this.$refs.sideMenu.updateActiveName();
        })
      break;
      case "addnewauthor":
        this.openSide = ["6"];
