  },
  data() {
    return {
      currentPath : location.pathname.split("/")[2],
      activeName:"",
      openSide:[],
      showExitModal: false
    }
  },
  methods: {
    backHome(){
      this.$router.push("/")
    },
    //头像移入改变icon
    exitIcon() {
      $('.avatar span>i').addClass('ivu-icon-power')
    },
    //头像移出还原icon
    userIcon() {
      $('.avatar span>i').removeClass('ivu-icon-power')
    },
    showModal() {
      this.showExitModal = true
    },
    loginOut() {
      this.$router.push('/login')
    },
    navLink(path){
      this.$router.push('/basicdataModal/'+path)
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/themeColor.less';
.home {
  .ivu-layout-header {
    height: 80px;
    line-height: 0; // 解决模态框点击确认后改变高度的问题
    border-bottom: 1px solid @themeColor;
    text-align: center;
    background: @bgc;
    .ivu-col {
      line-height: 80px;
      img {
        width: 220px;
        height: 60px;
        margin: 10px 0 0 10px;
      }
      .ivu-avatar {
        background-color: @themeColor;
      }
      .username {
        font-size: 20px;
      }
    }
    .userInfo {
      .ivu-btn{
        margin-right:50px;
        &:hover{
          border-color:@themeColor;
          color:@themeColor;
        }
      }
      .avatar {
        height: 80px;
        overflow: hidden;
      }
    }
  }
  .ivu-layout-sider {
    background-color: @themeColor;
    .ivu-menu-vertical .ivu-menu-item, 
    .ivu-menu-vertical .ivu-menu-submenu-title{
      height:50px;
      line-height:50px;
      padding:0 0 0 24px;
      span{
        display:inline-block;
        width:80%;
      }
    }
  }
  .ivu-menu-light {
    color: #fff;
    background-color: @themeColor;
    width:201px !important;
  }
  .ivu-menu-item {
    font-size:14px;
    color: #fff;
  }
  .ivu-menu-submenu{
    font-size:14px;
    .ivu-menu-item {
      text-align:center;
      font-size:12px;
    }
  }
  #basicDataSider,
  #chemoTherapySider,
  #TargetedTherapySider{
    margin-top:50px;
  }
  .icon{
    width: 15px;
    height: 15px;
    margin-right:5px;
    vertical-align: middle;
    display:inline-block;
  }
  .ivu-layout-footer {
    width: 100%;
    height: 30px;
    line-height: 30px;
    padding:0;
    font-size: 12px;
    color: #666;
    text-align: center;
  }
}
</style>



