<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/gene">基因管理</BreadcrumbItem>
      <BreadcrumbItem>转录本详情</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <span>Gene Symbol:{{currentGene.Symbol}}</span>
        <span>基因名称:{{currentGene.GeneName}}</span>
        <span>当前使用的转录本编号:{{currentGene.TranscriptID}}</span>
        <span>当前使用的转录本名称:{{currentGene.TranscriptName}}</span>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addNewTranscript">添加新转录本</Button>
      </div>
      <div class="transcriptListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" @click="batchRemove" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/gene/transcript/index.php",qs.stringify({GeneID:this.geneID})).then(response=>{
        console.log(response.data);
        this.tableData = response.data.data;
        this.totalNum = response.data.num;
        
        //判断该基因是否含有转录本，防止页面报错
        if(response.data.data.length === 0){
          this.$Message.warning("该基因尚未添加转录本记录！")
          this.loading = false;
          return false;
        }
        let obj = this.tableData.filter(item=>{
          return item.IsCurrentUsedTranscript==="yes"
        })

        //判断该基因时候有默认使用的转录本，防止页面报错
        if(obj.length===0){
          this.$Message.warning("该基因未选择当前使用的转录本")
          this.currentGene.TranscriptID = '';
          this.currentGene.TranscriptName = '';
          this.loading = false;
          return false;
        }

        this.currentGene.TranscriptID = obj[0].TranscriptID;
        this.currentGene.TranscriptName = obj[0].TranscriptName;
        this.loading = false;
      })
      this.$http.post("/php/gene/index.php",qs.stringify({GeneID:this.geneID})).then(response=>{
        // console.log(response.data);
        this.currentGene.Symbol = response.data.data[0].Symbol;
        this.currentGene.GeneName = response.data.data[0].GeneName;
      })
    },
    data() {
      return {
        geneID:location.search.split("=")[1],
        currentGene:{
          Symbol:'',
          GeneName:'',
          TranscriptID:'',
          TranscriptName:''
        },
        loading:true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"转录本编号",
            key:'TranscriptID',
            align: 'center'
          },
          {
            title:"转录本名称",
            key:'TranscriptName',
            align: 'center'
          },
          {
            title:"是否是当前使用的转录本",
            key:'IsCurrentUsedTranscript',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后修改日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.TranscriptID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.TranscriptID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.TranscriptID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        totalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      addNewTranscript(){
        this.$router.push("/basicdatamodal/addnewtranscript?geneid=" + this.geneID)
      },
      show (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/browtranscriptdetail?transcriptid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/edittranscriptdetail?transcriptid=" + id)
      },
      remove(id){
        // console.log(id);
        this.loading = true;
        this.$http.post("/php/gene/transcript/del.php",qs.stringify({TranscriptID:[id]})).then(response=>{
          // console.log(response.data);
          if(response.data){
            this.$Message.success("删除成功！正在刷新页面...")
            this.handlePage(this.pageNum);
          }else{
            this.$Message.error("删除失败！")
          }
          this.loading=false;
        })
      },
      batchRemove(){
        this.loading = true;
        let IDS=[];
        this.multipleSelection.forEach(item=>{
          IDS.push(item.TranscriptID)
        })
        this.$http.post("/php/gene/transcript/del.php",qs.stringify({TranscriptID:IDS})).then(response=>{
          // console.log(response.data);
          if(response.data){
            this.$Message.success("删除成功！正在刷新页面...")
            this.handlePage(this.pageNum);
          }else{
            this.$Message.error("删除失败！")
          }
          this.loading=false;
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        let obj = {}
        obj.GeneID=this.geneID;
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        this.$http.post("/php/gene/transcript/index.php",qs.stringify(obj)).then(response=>{
          // console.log(response.data);
          this.tableData = response.data.data;
          this.totalNum = response.data.num;
          this.loading=false;
        })
      },
      handlePageSize(val){
        this.loading = true;
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    span{
      font-size:14px;
      color:@themeColor;
      margin: 0 20px;
    }
  }
  .addContainer{
    padding:10px;

  }
  .transcriptListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


