<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/disease">疾病信息</BreadcrumbItem>
      <BreadcrumbItem>添加新的疾病</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的疾病</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的疾病</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
        <FormItem label="疾病编号" prop="DiseaseID">
              <Input v-model="addForm.DiseaseID" autofocus placeholder="请输入疾病编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病名称（英文）" prop="DiseaseName_en">
              <Input v-model="addForm.DiseaseName_en" placeholder="请输入疾病名称（英文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病名称（中文）" prop="DiseaseName_cn">
            <Input v-model="addForm.DiseaseName_cn" placeholder="请输入疾病名称（英文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="ICD编号">
              <Input v-model="addForm.ICDID" placeholder="请输入ICD编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          DiseaseID:'',
          DiseaseName_en:'',
          DiseaseName_cn:'',
          ICDID:''
        },
        rule:{
          DiseaseID:[
            {
              required: true, message: '疾病编号不能为空', trigger: 'blur'
            }
          ],
          DiseaseName_en:[
            {
              required: true, message: '疾病名称（英文）不能为空', trigger: 'blur'
            }
          ],
          DiseaseName_cn:[
            {
              required: true, message: '疾病名称（英文）不能为空', trigger: 'blur'
            }
          ],
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {};
            for(var key in this.addForm){
              obj[key] = this.addForm[key].trim()
            }
            this.$http.post('/php/disease/add.php',qs.stringify(obj)).then(response=>{
              // console.log(response.data);
              if(response.data==='wrong'){
                this.$Message.error('该疾病已存在！请检查输入内容');
                return false;
              }
              if(response.data===0){
                this.$Message.error('疾病编号已存在！请重新输入');
              }else{
                if(response.data){
                  this.$Message.success('添加成功，即将跳转页面...');
                  setTimeout(()=>{
                    this.$router.push('/basicdataModal/disease')
                  },1000)
                }else{
                  this.$Message.error('添加失败，请检查输入的内容');
                }
              }
            })
          }
        })
      },
      backList() {
        this.$router.push('/basicdataModal/disease')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


