<template>
  <div class="relatedContainer">
    <div class="deleteContainer">
      <Button type="warning" @click="remove">删除</Button>
    </div>
    <div class="editContainer">
      <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
        <FormItem label="药物编号" prop="DrugID">
          <Input v-model="editForm.DrugID" disabled placeholder="请输入药物编号" style="width: 280px"></Input>
        </FormItem>
        <FormItem label="药物名称（英文）" prop="DrugName_en">
          <AutoComplete v-model="editForm.DrugName_en" autofocus :disabled="step" @on-search="searchDrug_en" @on-select="selectDrug_en" placeholder="请选择药物名称（英文）" style="width:280px">
              <Option v-for="(item, index) in drugData" :value="item.DrugName_en" :key="index">{{ item.DrugName_en }}</Option>
            </AutoComplete>
        </FormItem>
        <FormItem label="药物名称（中文）">
          <AutoComplete v-model="editForm.DrugName_cn" :disabled="step" @on-search="searchDrug_cn" @on-select="selectDrug_cn" placeholder="请输入药物名称（中文）" style="width:280px">
              <Option v-for="(item, index) in drugData" :value="item.DrugName_cn" :key="index">{{ item.DrugName_cn }}</Option>
            </AutoComplete>
        </FormItem>
        <FormItem label="药物类别">
          <Input v-model="editForm.DrugClassName_en" disabled placeholder="请输入药物类别" style="width: 280px"></Input>
        </FormItem>
        <FormItem :label-width="10">
          <Button type="primary" @click="handleEdit" v-if="this.currentPath==='browdrugdetail'">编辑</Button>
          <Button type="primary" @click="handleSubmit('editForm')" v-if="this.currentPath==='editdrugdetail'">保存</Button>
          <Button @click="backList">返回</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      this.currentPath==="browdrugdetail"?this.step = true:this.step = false;
      //获取当前条目的数据
      this.$http.post("/php/targetedtherapy2/editTherapy.php",qs.stringify({
        TargetTreatmentTherapyDrugID:this.targetTreatmentTherapyDrugID
      })).then(response=>{
        console.log(response.data);
        this.editForm = response.data.list[0];
      })
      //获取所有药物的信息，为自动完成效果做准备
      this.$http.post("/php/targetedtherapy2/getDrug.php").then(response=>{
        this.drugArr = response.data.list;
        this.drugData = response.data.list;
        // console.log(this.drugData);
      })
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[3],
        targetTreatmentTherapyDrugID:location.search.split("=")[1],
        drugArr:'',
        drugData:'',
        step:false,
        deleteModal:false,
        editForm:{},
        rule:{
          DrugID:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ],
          DrugName_en:[
            {
              required:true,message:'不能为空，请选择',trigger:'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      handleEdit(name){
        this.currentPath = 'editdrugdetail'
        this.step = false;
      },
      remove(){
        this.$http.post("/php/targetedtherapy2/delTherapy.php",qs.stringify({
          TargetTreatmentTherapyDrugID:[this.editForm.TargetTreatmentTherapyDrugID]
        })).then(response=>{
          // console.log(response.data);
          if(response.data){
            this.$Message.success("删除成功！即将跳转页面...")
            setTimeout(()=>{
              this.$router.push('/targetedtherapymodal/browtargetedtherapy2detail/druglist?therapy2=' + this.editForm.TargetTreatmentTherapyID)
            },1000)
          }else{
            this.$Message.error("删除失败！")
          }
        })
      },
      searchDrug_en(value){
        this.$http.post("/php/targetedtherapy2/getDrug.php",qs.stringify({
          DrugName_en:value
        })).then(response=>{
          this.drugArr = response.data.list;
          this.drugData = response.data.list;
          // console.log(this.drugData);
        })
      },
      selectDrug_en(value){
        this.editForm.DrugName_en = value;
        this.drugData = this.drugArr.filter(item=>{
          return item.DrugName_en === value
        })
        // console.log(this.drugData);
        this.editForm.DrugID = this.drugData[0].DrugID;
        this.editForm.DrugName_cn = this.drugData[0].DrugName_cn;
        this.editForm.DrugClassName_en = this.drugData[0].DrugClassName_en;
      },
      searchDrug_cn(value){
        this.$http.post("/php/targetedtherapy2/getDrug.php",qs.stringify({
          DrugName_cn:value
        })).then(response=>{
          this.drugArr = response.data.list;
          this.drugData = response.data.list;
          // console.log(this.drugData);
        })
      },
      selectDrug_cn(value){
        this.editForm.DrugName_cn = value;
        this.drugData = this.drugArr.filter(item=>{
          return item.DrugName_cn === value
        })
        // console.log(this.drugData);
        this.editForm.DrugID = this.drugData[0].DrugID;
        this.editForm.DrugName_en = this.drugData[0].DrugName_en;
        this.editForm.DrugClassName_en = this.drugData[0].DrugClassName_en;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
           if(this.drugData[0].DrugName_en===this.editForm.DrugName_en){
              this.$http.post("/php/targetedtherapy2/updateTherapy.php",qs.stringify({
                TargetTreatmentTherapyDrugID:this.editForm.TargetTreatmentTherapyDrugID,
                DrugID:this.editForm.DrugID
              })).then(response=>{
                console.log(response.data);
                if(response.data==="wrong"){
                  this.$Message.error("该药物已被添加，请重新输入")
                  return false;
                }
                if(response.data){
                  this.$Message.success("保存成功！即将跳转页面...")
                  setTimeout(()=>{
                    this.$router.push('/targetedtherapymodal/browtargetedtherapy2detail/druglist?therapy2=' + this.editForm.TargetTreatmentTherapyID)
                  },1000)
                }else{
                  this.$Message.error("保存失败！请检查输入内容")
                }
              })
            }else{
              this.$Message.error("药物英文名称输入错误，请检查")
            }
          } 
        })
      },
      backList() {
        this.$router.push('/targetedtherapymodal/browtargetedtherapy2detail/druglist?therapy2=' + this.editForm.TargetTreatmentTherapyID)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.relatedContainer{
  padding-top:20px;
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


