<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/snpindel">SNP_InDel</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="deleteModal=true">删除</Button>
        <Modal v-model="deleteModal" @on-ok="remove">
          <p>确认删除此条目吗？</p>
        </Modal>
      </div>
      <div class="browContainer">
        <Form :inline="true" ref="browForm" :model="browForm" label-position="right" :label-width="100">
          <FormItem label="创建日期">
            <Input v-model="browForm.CreateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后修改日期">
            <Input v-model="browForm.LatestUpdateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="变异编号">
            <Input v-model="browForm.VariantID" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Gene Symbol">
            <Input v-model="browForm.Symbol" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体名称">
            <Input v-model="browForm.ChrName" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体位置">
            <Input v-model="browForm.Position" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="参考基因组序列">
            <Input v-model="browForm.REF_base" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="ALT序列">
            <Input v-model="browForm.ALT_base" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="转录本信息">
            <Input v-model="browForm.TranscriptName" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSp">
            <Input v-model="browForm.HGVSp" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSc">
            <Input v-model="browForm.HGVSc" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Impact">
            <Input v-model="browForm.Impact" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床意义">
            <Input v-model="browForm.Clin_Sig" readonly style="width: 675px"></Input>
          </FormItem>
          <div>
            <FormItem label="英文功能描述">
              <Input v-model="browForm.functionDescEN" type="textarea" readonly :rows="4" style="width: 530px"></Input>
              <Button type="dashed" shape="circle" style="width:120px">添加至分配列表</Button>
            </FormItem>
          </div>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit">编辑</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
      <div class="relatedContainer">
        <div class="deleteContainer">
          <Button type="warning">关联新证据</Button>
        </div>
        <Table stripe border ref="selection" height="200"  :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
import {sendChrID,reciveChrID} from "../../../../assets/switchChrName2ID.js"
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/vrsnpindel/",qs.stringify({
        VariantID:this.variantID
      })).then(response=>{
        // console.log(response.data);
        if(response.data.data.length!==0){
          this.browForm = response.data.data[0]
          this.browForm.ChrName = reciveChrID(this.browForm.ChrID)
        }else{
          this.$Message.error("该变异不存在！即将跳转回列表页...")
          setTimeout(()=>{
            this.$router.push('/basicdataModal/snpindel')
          },1000)
        }
      })
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        variantID:location.search.split("=")[1],
        deleteModal:false,
        browForm:{},
        multipleSelection:[],
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"证据等级",
            key:'evidenceLevel',
            align: 'center'
          },
          {
            title:"证据描述",
            key:'evidenceDesc',
            align: 'center'
          },
          {
            title:"最后更新时间",
            key:'updateDate',
            align: 'center'
          },
          {
            title:"更新人",
            key:'updatePreson',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.index)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            evidenceDesc:"11111",
            evidenceLevel:8,
            updateDate:"2018-03-06",
            updatePreson:"普希金"
          },
           {
            evidenceDesc:"11111",
            evidenceLevel:8,
            updateDate:"2018-03-06",
            updatePreson:"普希金"
          },
          {
            evidenceDesc:"11111",
            evidenceLevel:8,
            updateDate:"2018-03-06",
            updatePreson:"普希金"
          }
        ],
      }
    },
    methods: {
      remove(){
        this.$http.post("/php/vrsnpindel/del.php",qs.stringify({
          VariantID:[this.variantID]
        })).then(response=>{
          // console.log(response.data);
          if(response.data){
            this.$Message.success('删除成功');
            this.$router.push('/basicdatamodal/snpindel')
          }else{           
            this.$Message.error('删除失败');
          }
        })
      },
      handleEdit(id){
        this.$router.push('/basicdatamodal/editsnpindeldetail?VariantID=' + this.variantID)
      },
      backList() {
        this.$router.push('/basicdatamodal/snpindel')
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .browContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relatedContainer{
    border-top:1px dotted #ccc;
    .batchContainer{
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
  }
}
</style>


