<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/chemotherapymodal/chemotherapy">化学药物治疗</BreadcrumbItem>
      <BreadcrumbItem>添加</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的化学药物治疗</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的化学药物治疗</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="变异位点HGVSp" prop="HGVSp">
            <AutoComplete v-model="addForm.HGVSp" @on-search="searchVariant" @on-select="selectVariant" placeholder="请选择变异位点HGVSp" style="width:280px">
              <Option v-for="(item, index) in VariantIDHGVSpArr" :value="item.HGVSp" :key="index">{{ item.HGVSp }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="基因型" prop="Genotype">
              <Input v-model="addForm.Genotype" placeholder="请输入基因型" style="width: 280px"></Input>
              <!--<AutoComplete v-model="addForm.Genotype" @on-search="searchGene" @on-select="selectGene" placeholder="请输入基因型" style="width:280px">
              <Option v-for="(item, index) in SymbolArr" :value="item.Symbol" :key="index">{{ item.Symbol }}</Option>
            </AutoComplete>-->
          </FormItem>
          <FormItem label="疾病编号" prop="DiseaseID">
            <AutoComplete v-model="addForm.DiseaseID" @on-search="searchDiseaseID" @on-select="selectDiseaseID" placeholder="请选择疾病编号" style="width:280px">
              <Option v-for="(item, index) in DiseaseArr" :value="item.DiseaseID" :key="index">{{ item.DiseaseID }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="疾病中文名称">
              <AutoComplete v-model="addForm.DiseaseName_cn" @on-search="searchDiseaseName" @on-select="selectDiseaseName" placeholder="请选择疾病中文名称" style="width:280px">
              <Option v-for="(item, index) in DiseaseArr" :value="item.DiseaseName_cn" :key="index">{{ item.DiseaseName_cn }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="药物编号" prop="DrugID">
            <AutoComplete v-model="addForm.DrugID" @on-search="searchDrugID" @on-select="selectDrugID" placeholder="请选择药物编号" style="width:280px">
            <Option v-for="(item, index) in DrugArr" :value="item.DrugID" :key="index">{{ item.DrugID }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="药物中文名称">
            <AutoComplete v-model="addForm.DrugName_cn" @on-search="searchDrugName" @on-select="selectDrugName" placeholder="请选择药物中文名称" style="width:280px">
              <Option v-for="(item, index) in DrugArr" :value="item.DrugName_cn" :key="index">{{ item.DrugName_cn }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="英文功能描述" prop="desc_en">
            <Input v-model="addForm.desc_en" type="textarea" :rows="4" placeholder="请输入英文功能描述" style="width: 280px"></Input>
            <Button type="dashed" shape="circle" style="width:120px">添加至分配列表</Button>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.all([
        this.$http.post("/php/chemotherapymodal/getVariant.php",qs.stringify({Variant:''})),
        this.$http.post("/php/chemotherapymodal/getDiseaseNum.php",qs.stringify({DiseaseID:'1',DiseaseName_cn:''})),
        this.$http.post("/php/chemotherapymodal/getDrugNum.php",qs.stringify({DrugID:'1',DrugName_cn:''}))
      ]).then(this.$http.spread((hgsvp,disease,drug)=>{
        this.VariantIDHGVSpArr = hgsvp.data;
        this.DiseaseArr = disease.data;
        this.DrugArr = drug.data;
      }))
    },
    data() {
      return {
        addForm: {
          HGVSp:'',
          VariantID:'',
          Genotype:'',
          DiseaseID:'',
          DiseaseName_cn:'',
          DrugID:'',
          DrugName_cn:'',
          desc_en:''
        },
        VariantIDHGVSpArr:[],
        DiseaseArr:[],
        DrugArr:[],
        rule:{
          HGVSp:[{
            required:true,message:'不能为空',trigger:'blur,change'
          }],
          Genotype:[{
            required:true,message:'不能为空',trigger:'blur,change'
          }],
          DiseaseID:[{
            required:true,message:'不能为空',trigger:'blur,change'
          }],
          DrugID:[{
            required:true,message:'不能为空',trigger:'blur,change'
          }]
        }
      }
    },
    methods: {
      searchVariant(value){
        this.$http.post("/php/chemotherapymodal/getVariant.php",qs.stringify({
          Variant:value
        })).then(response=>{
          // console.log(response.data);
          this.VariantIDHGVSpArr = response.data
          if(this.VariantIDHGVSpArr.length===0){
            this.$Message.error('变异位点错误,请重新输入');
          }
        })
      },
      selectVariant(value){
        this.addForm.HGVSp = value;
        let arr = this.VariantIDHGVSpArr.filter(item=>{
          return item.HGVSp === value
        })
        this.addForm.VariantID = arr[0].VariantID;
        // console.log(this.addForm.VariantID);
      },
      searchDiseaseID(value){
        this.$http.post("/php/chemotherapymodal/getDiseaseNum.php",qs.stringify({
          DiseaseID:value
        })).then(response=>{
          // console.log(response.data);
          this.DiseaseArr = response.data
          if(this.DiseaseArr.length===0){
            this.$Message.error('疾病编号错误,请重新输入');
          }
        })
      },
      selectDiseaseID(value){
        // console.log(value);
        this.addForm.DiseaseID = value;
        this.addForm.DiseaseName_cn = this.DiseaseArr.filter(item=>{
          return item.DiseaseID.indexOf(value) !== -1
        })[0].DiseaseName_cn;
      },
      searchDiseaseName(value){
        this.$http.post("/php/chemotherapymodal/getDiseaseNum.php",qs.stringify({
          DiseaseName_cn:value
        })).then(response=>{
          // console.log(response.data);
          this.DiseaseArr = response.data
          if(this.DiseaseArr.length===0){
            this.$Message.error('疾病中文名称错误,请重新输入');
          }
        })
      },
      selectDiseaseName(value){
        // console.log(value);
        this.addForm.DiseaseName_cn = value;
        this.addForm.DiseaseID = this.DiseaseArr.filter(item=>{
          return item.DiseaseName_cn.indexOf(value) !== -1
        })[0].DiseaseID;
      },
      searchDrugID(value){
        // console.log(value);
        this.$http.post("/php/chemotherapymodal/getDrugNum.php",qs.stringify({
          DrugID:value
        })).then(response=>{
          // console.log(response.data);
          this.DrugArr = response.data
          if(this.DrugArr.length===0){
            this.$Message.error('药物编号错误,请重新输入');
          }
        })
      },
      selectDrugID(value){
        // console.log(value);
        this.addForm.DrugID = value;
        this.addForm.DrugName_cn = this.DrugArr.filter(item=>{
          return item.DrugID.indexOf(value) !== -1
        })[0].DrugName_cn;
      },
      searchDrugName(value){
        this.$http.post("/php/chemotherapymodal/getDrugNum.php",qs.stringify({
          DrugName_cn:value
        })).then(response=>{
          // console.log(response.data);
          this.DrugArr = response.data
          if(this.DrugArr.length===0){
            this.$Message.error('药物中文名称错误,请重新输入');
          }
        })
      },
      selectDrugName(value){
        // console.log(value);
        this.addForm.DrugName_cn = value;
        this.addForm.DrugID = this.DrugArr.filter(item=>{
          return item.DrugName_cn.indexOf(value) !== -1
        })[0].DrugID;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(this.VariantIDHGVSpArr.length===0||this.addForm.HGVSp.trim()===''){
              this.$Message.error('变异位点错误,请重新输入');
              return false;
            }
            if(this.addForm.Genotype.trim()===''){
              this.$Message.error('基因型错误,请重新输入');
              return false;
            }
            if(this.DiseaseArr.length===0||this.addForm.DiseaseID.trim()===''){
              this.$Message.error('疾病编号或名称错误,请检查');
              return false;
            }
            if(this.DrugArr.length===0||this.addForm.DrugID.trim()===''){
              this.$Message.error('药物编号或名称错误,请检查');
              return false;
            }
            let obj = {};
            obj.VariantID = this.addForm.VariantID.trim();
            obj.Genotype = this.addForm.Genotype.trim();
            obj.DiseaseID = this.addForm.DiseaseID.trim();
            obj.DrugID = this.addForm.DrugID.trim();
            this.$http.post("/php/chemotherapymodal/add.php",qs.stringify(obj)).then(response=>{
              // console.log(response.data);
              if(response.data){
                this.$Message.success('添加成功，即将跳转页面...');
                this.$router.push("/chemotherapymodal/chemotherapy")
              }else{
                this.$Message.error('添加失败,请检查页面输入');
              }
            })
            
          }
        })
      },
      backList(){
        this.$router.push("/chemotherapymodal/chemotherapy")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


