<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/snpindel">SNP_InDel</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="deleteModal=true">删除</Button>
        <Modal v-model="deleteModal" @on-ok="remove">
          <p>确认删除此条目吗？</p>
        </Modal>
      </div>
      <div class="editContainer">
        <Form :inline="true" ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后修改日期">
            <Input v-model="editForm.LatestUpdateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="变异编号">
            <Input v-model="editForm.VariantID" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Gene Symbol" prop="Symbol">
            <AutoComplete v-model="editForm.Symbol" @on-search="searchSymbol" @on-select="selectSymbol" placeholder="请选择Gene Symbol" style="width:280px">
              <Option v-for="(item, index) in geneSymbolData" :value="item" :key="index">{{ item }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="染色体名称" prop="ChrName">
            <AutoComplete v-model="editForm.ChrName" @on-search="searchChromo" @on-select="selectChromo" placeholder="请输入染色体名称" style="width:280px">
              <Option v-for="(item, index) in chromoData" :value="item" :key="index">{{ item }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="染色体位置">
            <Input v-model="editForm.Position" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="参考基因组序列">
            <Input v-model="editForm.REF_base" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="ALT序列">
            <Input v-model="editForm.ALT_base" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="转录本信息">
            <Input v-model="editForm.TranscriptName" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSp">
            <Input v-model="editForm.HGVSp" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="HGVSc">
            <Input v-model="editForm.HGVSc" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Impact">
            <Input v-model="editForm.Impact" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床意义">
            <Input v-model="editForm.Clin_Sig" style="width: 675px"></Input>
          </FormItem>
          <div>
            <FormItem label="英文功能描述">
              <Input v-model="editForm.functionDescEN" type="textarea" :rows="4" style="width: 530px"></Input>
              <Button type="dashed" shape="circle" style="width:120px">添加至分配列表</Button>
            </FormItem>
          </div>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
import {sendChrID,reciveChrID} from "../../../../assets/switchChrName2ID.js"
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/vrsnpindel/",qs.stringify({
        VariantID:this.variantID
      })).then(response=>{
        // console.log(response.data);
        if(response.data.data.length!==0){
          this.editForm = response.data.data[0];
          this.editForm.ChrName = reciveChrID(this.editForm.ChrID)
        }else{
          this.$Message.error("该变异不存在！即将跳转回列表页...")
          setTimeout(()=>{
            this.$router.push('/basicdataModal/snpindel')
          },1000)
        }
      })
      // this.$http.post('/php/gsymbol/').then(response=>{
      //   // console.log(response.data);
      //   this.geneSymbolArr = response.data;
      //   this.geneSymbolData = this.geneSymbolArr;
      // })
      this.$http.all([
        this.$http.post("/php/gsymbol/index.php"),
        this.$http.post("/php/chromo/index.php")
      ]).then(this.$http.spread((symbol,chromo)=>{
        this.geneSymbolArr = symbol.data;
        this.geneSymbolData = this.geneSymbolArr;
        chromo.data.forEach(item=>{
          this.chromoArr.push(item.ChrName)
          this.chromoData.push(item.ChrName)
        })
      }))
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        variantID:location.search.split("=")[1],
        geneSymbolArr:[],
        geneSymbolData:[],
        chromoArr:[],
        chromoData:[],
        deleteModal:false,
        editForm:{},
        rule:{
          Symbol:[
            {
              required: true, message: 'Gene Symbol不能为空', trigger: 'blur'
            }
          ],
          ChrName:[
            {
              required: true, message: 'Gene Symbol不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      remove(){
        this.$http.post("/php/vrsnpindel/del.php",qs.stringify({
          VariantID:[this.variantID]
        })).then(response=>{
          // console.log(response.data);
          if(response.data){
            this.$Message.success('删除成功！即将跳转页面...');
            setTimeout(()=>{
              this.$router.push('/basicdatamodal/snpindel')
            },1000)
          }else{           
            this.$Message.error('删除失败!');
          }
        })
      },
      searchSymbol(value){
        this.$http.post("/php/gsymbol/index.php",qs.stringify({
          Symbol:value
        })).then(response=>{
          // console.log(response.data);
          this.geneSymbolArr = response.data;
          this.geneSymbolData = this.geneSymbolArr.filter(item=>{
            return item.toUpperCase().indexOf(value.toUpperCase()) !== -1
          })
        })
      },
      selectSymbol(value){
        this.editForm.Symbol = value;
        // console.log(value);
      },
      searchChromo(value){
        this.chromoData = this.chromoArr.filter(item=>{
          return item.toUpperCase().indexOf(value.toUpperCase()) !== -1
        })
      },
      selectChromo(value){
        // console.log(value);
        this.editForm.ChrName = value;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            //判断Symbol是否存在
            let count=0;
            this.geneSymbolArr.forEach(item=>{
              if(item===this.editForm.Symbol){
                count++;
              }
            })
            if(count===0){
              this.$Message.error('Gene Symbol错误，请重新选择');
              return false;
            }
            let obj = this.editForm;
            obj.ChrID = sendChrID(obj.ChrName)
            if(obj.ChrID!='error'){
              this.$http.post("/php/vrsnpindel/edit.php",qs.stringify(obj)).then(response=>{
                // console.log(response.data);
                if(response.data===0){
                  this.$Message.error('Gene Symbol错误！请重新选择');
                }else{
                  if(response.data){
                    this.$Message.success('保存成功！即将跳转页面...');
                    setTimeout(()=>{
                      this.$router.push('/basicdataModal/snpindel')   
                    },1000)
                  }else{
                    this.$Message.error('保存失败！请检查输入的内容');
                  }
                }
              })
            }else{
              this.$Message.error('染色体名称错误！请重新选择');
            }
          } 
        })
      },
      backList() {
        this.$router.push('/basicdatamodal/snpindel')
      },
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relatedContainer{
    border-top:1px dotted #ccc;
    .batchContainer{
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
  }
}
</style>


