<template>
  <!--<div>{{row.DrugClassName_en.split(",")}}</div>-->
  <ul class="drugClassTpl">
    <!-- 只有一个类别 -->
    <li v-if="row.DrugClassName_en.indexOf(',')===-1">{{row.DrugClassName_en}}</li>
    <!-- 多个类别 -->
    <li v-if="row.DrugClassName_en.indexOf(',')!==-1" v-for="(item,index) in row.DrugClassName_en.split(',')">{{item}}</li>
  </ul>
</template>
<script>
  export default {
      props: {
          row: Object
      }
    };
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
  .drugClassTpl{
    li{
      color:#fff !important;
      display:inline-block !important;
      background-color:@themeColor !important;
      padding:5px !important;
      margin:1px !important;
    }
  }
</style>


