<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/chromo">染色体管理</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='browchromodetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='editchromodetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <!--<Button type="warning">删除</Button>-->
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最新修改日期">
            <Input v-model="editForm.LatestUpdateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体编号">
            <Input v-model="editForm.ChrID" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体名称" prop="ChrName">
              <Input v-model="editForm.ChrName" :disabled="this.currentPath==='browchromodetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体长度">
            <Input v-model="editForm.Length" :disabled="this.currentPath==='browchromodetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="拼接版本">
              <Input v-model="editForm.AssemblyVersion" :disabled="this.currentPath==='browchromodetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit('editForm')" v-if="this.currentPath==='browchromodetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editForm')" v-if="this.currentPath==='editchromodetail'">保存</Button>
            <Button @click="handleSubmit('editForm')">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs");
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/chromo/index.php",qs.stringify({ChrID:this.chrID})).then(response=>{
        // console.log(response.data[0]);
        this.editForm = response.data[0]

      })
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        chrID:location.search.split("=")[1],
        editForm:{
          CreateDate:'',
          LatestUpdateDate:'',
          ChrID:'',
          ChrName:'',
          Length:'',
          AssemblyVersion:''
        },
        rule:{
          ChrName:[
            {
              required: true, message: '染色体名称不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleEdit(name){
        this.currentPath = 'editchromodetail'
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$http.post("/php/chromo/edit.php",qs.stringify(this.editForm)).then(response=>{
              // console.log(response.data);
              if(response.data){
                this.$Message.success('提交成功，即将跳转页面...');
                setTimeout(()=>{
                this.$router.push('/basicdataModal/chromo')              
                },1000)
              }else{
                this.$Message.error('提交失败，请检查输入的内容');
              }
            })
            this.$router.push('/basicdataModal/chromo')
          }
        })
        
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


