<template>
  <div class="dashboard">
    <div>
      <header>
        <h1>
          <img src="../../static/imgs/logo_ky.png" alt="鹍远基因" title="鹍远基因">
          <Button type="dashed" icon="log-out" class="loginOutIcon" @click="loginOut">退出</Button>
        </h1>
        <h2>知识库管理系统</h2>
      </header>
      <Card style="width:510px;height:120px;margin-bottom:10px">
        <p @click="basic">基础知识</p>
      </Card>
      <!--<Card :bordered="false" style="width:250px;height:120px;float:right;margin-bottom:10px">
        <p>变异注释</p>
      </Card>-->
      <Card style="width:250px;height:120px;float:left">
        <p @click="chromo">化学药物治疗</p>
      </Card>
      <Card style="width:250px;height:120px;float:right">
        <p @click="target">靶向药物治疗</p>
      </Card>
    </div>
    <footer class="loginFooter">Singlera Genomic Inc | Copyright 2018</footer>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        
      }
    },
    methods:{
      loginOut(){
        this.$router.push("/login")
      },
      basic(){
        this.$router.push("/basicdatamodal/chromo")
      },
      chromo(){
        this.$router.push("/chemotherapymodal/chemotherapy")
      },
      target(){
        this.$router.push("/targetedtherapymodal/clinicaltrial")
      }
    }
  }
</script>
<style lang="less" scoped>
  .dashboard {
  height: 100%;
  position: relative;
  >div:nth-child(1) {
    width: 510px;
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
    header {
      height: 60px;
      line-height: 60px;
      img {
        float: left;
        width: 180px;
        height: 50px;
        margin-top: -50px;
      }
      .loginOutIcon{
        float:right;
        font-size:12px;
        margin-top:-45px;
      }
      h2 {
        text-align: center;
        font-size: 18px;
      }
    }
    .ivu-card{
      font-size: 16px;
      text-align: center;
      line-height: 85px;
    }
  }
}
</style>


