<template>
  <div class="login">
    <Form ref="formLogin" :model="formLogin" :rules="ruleLogin">
      <header>
        <h1>
          <img src="../../static/imgs/logo_ky.png" alt="鹍远基因" title="鹍远基因">
        </h1>
        <h2>知识库管理系统</h2>
      </header>
      <FormItem prop="user">
        <div class="inputCon">
          <Input type="text" v-model="formLogin.user" placeholder="Username">
              <Icon type="person" slot="prepend" size=16></Icon>
          </Input>
        </div>
      </FormItem>
      <FormItem prop="password">
        <div class="inputCon" @keyup.enter="handleSubmit('formLogin')">
          <Input type="password" v-model="formLogin.password" placeholder="Password">
              <Icon type="locked" slot="prepend" size=14></Icon>
          </Input>
        </div>
      </FormItem>
      <FormItem>
          <Button type="primary" @click="handleSubmit('formLogin')" long size="large">登录</Button>
      </FormItem>
    </Form>
    <footer class="loginFooter">Singlera Genomic Inc | Copyright 2018</footer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formLogin: {
        user: 'root',
        password: '123456'
      },
      ruleLogin: {
        user: [
          {
            required: true,
            message: '请输入用户名',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          },
          {
            min: 6,
            message: 'The password length cannot be less than 6 bits',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.$cookies.set('user',this.formLogin.user,60*60*24)
          this.$Message.success('登录成功，正在跳转...')
          this.$router.push('/')
        } else {
          this.$Message.error('登录失败，请检查用户名和密码')
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.login {
  height: 100%;
  position: relative;
  form {
    width: 300px;
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
    header {
      height: 60px;
      line-height: 60px;
      img {
        float: left;
        width: 180px;
        height: 50px;
        margin-top: -50px;
      }
      h2 {
        text-align: center;
        font-size: 18px;
      }
    }
    .inputCon{
      font-size: 14px;
    }
  }
}
</style>



