<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>转录本详情</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='browtranscriptdetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='edittranscriptdetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="remove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" label-position="left" :label-width="120">
          <FormItem label="Gene Symbol">
              <Input v-model="editForm.Symbol" disabled placeholder="请选择Gene Symbol" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="基因名称">
              <Input v-model="editForm.GeneName" disabled placeholder="请输入基因名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="转录本编号">
            <Input v-model="editForm.TranscriptID" disabled placeholder="请输入转录本编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="转录本名称">
              <Input v-model="editForm.TranscriptName" :disabled="this.currentPath==='browtranscriptdetail'" placeholder="请输入转录本名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="是否设置为当前使用的转录本" prop="isCurrentTrans">
            <RadioGroup v-model="editForm.IsCurrentUsedTranscript">
              <Radio label="yes">yes</Radio>
              <Radio label="no">no</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit('editForm')" v-if="this.currentPath==='browtranscriptdetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editForm')" v-if="this.currentPath==='edittranscriptdetail'">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/gene/transcript/index.php",qs.stringify({TranscriptID:this.transcriptID})).then(response=>{
        // console.log(response.data);
        this.editForm = response.data.data[0]
      })
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        transcriptID:location.search.split("=")[1],
        editForm:{}
      }
    },
    methods: {
      remove(){
        this.$http.post("/php/gene/transcript/del.php",qs.stringify({TranscriptID:[this.transcriptID]})).then(response=>{
          // console.log(response.data);
          if(response.data){
            this.$Message.success("删除成功！即将跳转页面...")
            setTimeout(()=>{
              this.$router.push('/basicdataModal/transcriptlist?geneID=' + this.editForm.GeneID)
            },1000)
          }else{
            this.$Message.error("删除失败！")
          }
        })
      },
      handleEdit(name){
        this.currentPath = 'edittranscriptdetail'
      },
      handleSubmit(name) {
        this.$http.post("/php/gene/transcript/edit.php",qs.stringify(this.editForm)).then(response=>{
          // console.log(response.data);
          if(response.data){
            this.$Message.success('保存成功，即将跳转页面...');
            setTimeout(()=>{
            this.$router.push('/basicdatamodal/transcriptlist?geneID=' + this.editForm.GeneID);      
            },1000)
          }else{
            this.$Message.error('保存失败，请检查输入的内容');
          }
        })
      },
      backList(){
        this.$router.push('/basicdatamodal/transcriptlist?geneID=' + this.editForm.GeneID)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


