<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>化疗治疗管理</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="变异位点(Symbol)">
            <Input v-model="searchForm.Symbol" placeholder="请输入变异名称"></Input>
          </FormItem>
          <FormItem label="变异位点(HGVSp)">
            <Input v-model="searchForm.HGVSp" placeholder="请输入变异名称"></Input>
          </FormItem>
          <FormItem label="基因型">
            <Input v-model="searchForm.Genotype" placeholder="请输入基因型"></Input>
          </FormItem>
          <FormItem label="疾病中文名称">
            <Input v-model="searchForm.DiseaseName_cn" placeholder="请输入疾病名称"></Input>
          </FormItem>
          <FormItem label="药物中文名称">
            <Input v-model="searchForm.DrugName_cn" placeholder="请输入药物名称"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addNewGene">添加新基因型、药物和疾病信息</Button>
      </div>
      <div class="chemoListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <template>
          <div></div>
        </template>
        <div class="batchContainer">
          <Button type="text" size="large" @click="batchRemove" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
import expandTpl from './expandTpl.vue'
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/chemotherapymodal/list.php").then(response=>{
        // console.log(response.data);
        this.totalNum = Number(response.data.total);
        this.tableData = response.data.list;
        this.tableData.forEach(item=>{
          item.VariantInfo = item.Symbol + ' ' + item.HGVSp;
        })
        this.tableData.VariantInfo = this.tableData.Symbol + '&' + this.tableData.HGVSp;
        this.loading = false;
      })
    },
    components:{expandTpl},
    data() {
      return {
        searchForm:{
          Symbol:'',
          HGVSp:'',
          Genotype:'',
          DiseaseName_cn:'',
          DrugName_cn:''
        },
        loading:true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"描述信息",
            type: 'expand',
            align: 'center',
            width: 80,
            render: (h, params) => {
              return h(expandTpl, {
                props: {
                  row: params.row
                }
              })
            }
          },
          {
            title:"变异信息",
            key:'VariantInfo',
            align: 'center',
            width: 100,
          },
          {
            title:"基因型",
            key:'Genotype',
            align: 'center'
          },
          {
            title:"疾病中文名称",
            key:'DiseaseName_cn',
            align: 'center'
          },
          {
            title:"药物中文名称",
            key:'DrugName_cn',
            align: 'center'
          },
          {
            title:"最后修改日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            width:150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.ChemtreaTmentID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.ChemtreaTmentID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ChemtreaTmentID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        totalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.loading = true;
        this.pageNum = 1;
        this.pageSize = 10;
        let obj = {};
        let count = 0;
        for(var key in this.searchForm){
          obj[key]=this.searchForm[key].trim()
          if(obj[key]!==""){
            count++;
          }
        }
        if(count === 0){
          this.$http.post("/php/chemotherapymodal/list.php",qs.stringify({
            page:this.pageNum,
            count:this.pageSize
          })).then(response=>{
            // console.log(response.data);
            this.totalNum = Number(response.data.total);
            this.tableData = response.data.list;
            this.tableData.forEach(item=>{
              item.VariantInfo = item.Symbol + ' ' + item.HGVSp;
            })
            this.tableData.VariantInfo = this.tableData.Symbol + '&' + this.tableData.HGVSp;
            this.loading = false;
          })
        }else{
          obj.page = this.pageNum;
          obj.count = this.pageSize;
          this.$http.post("/php/chemotherapymodal/search.php",qs.stringify(obj)).then(response=>{
            // console.log(response.data);
            this.totalNum = Number(response.data.total);
            this.tableData = response.data.list;
            this.tableData.forEach(item=>{
              item.VariantInfo = item.Symbol + ' ' + item.HGVSp;
            })
            this.tableData.VariantInfo = this.tableData.Symbol + '&' + this.tableData.HGVSp;
            this.loading = false;
          })
        }
      },
      addNewGene(){
        this.$router.push("/chemotherapymodal/addnewchemotherapy")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/chemotherapymodal/browchemotherapydetail?chemoid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/chemotherapymodal/editchemotherapydetail?chemoid=" + id)
      },
      remove(id){
        // console.log(id);
        this.loading = true;
        this.$http.post("/php/chemotherapymodal/del.php",qs.stringify({
          ChemtreaTmentID:[id]
        })).then(response=>{
          if(response.data){
            this.$Message.success("删除成功！正在刷新页面...")
            this.handlePage(this.pageNum);
          }else{
            this.$Message.error('删除失败！');
          }
          this.loading = false;
        })
      },
      batchRemove(){
        this.loading = true;
        let IDS = [];
        this.multipleSelection.forEach(item=>{
          IDS.push(item.ChemtreaTmentID)
        })
        // console.log(IDS);
        this.$http.post("/php/chemotherapymodal/del.php",qs.stringify({
          ChemtreaTmentID:IDS
        })).then(response=>{
          if(response.data){
            this.$Message.success('批量删除成功！正在刷新页面...');
            this.handlePage(this.pageNum);
          }else{
            this.$Message.success('删除失败！');
          }
          this.loading = false;
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        let obj = {};
        let count = 0;
        for(var key in this.searchForm){
          obj[key]=this.searchForm[key].trim()
          if(obj[key]!==""){
            count++;
          }
        }
        if(count === 0){
          this.$http.post("/php/chemotherapymodal/list.php",qs.stringify({
            page:this.pageNum,
            count:this.pageSize
          })).then(response=>{
            // console.log(response.data);
            this.totalNum = Number(response.data.total);
            this.tableData = response.data.list;
            this.tableData.forEach(item=>{
              item.VariantInfo = item.Symbol + ' ' + item.HGVSp;
            })
            this.tableData.VariantInfo = this.tableData.Symbol + '&' + this.tableData.HGVSp;
            this.loading = false;
          })
        }else{
          obj.page = this.pageNum;
          obj.count = this.pageSize;
          this.$http.post("/php/chemotherapymodal/search.php",qs.stringify(obj)).then(response=>{
            // console.log(response.data);
            this.totalNum = Number(response.data.total);
            this.tableData = response.data.list;
            this.tableData.forEach(item=>{
              item.VariantInfo = item.Symbol + ' ' + item.HGVSp;
            })
            this.tableData.VariantInfo = this.tableData.Symbol + '&' + this.tableData.HGVSp;
            this.loading = false;
          })
        }     
      },
      handlePageSize(val){
        this.loading = true;
        this.pageSize = val;
        this.pageNum = 1;
        let obj = {};
        let count = 0;
        for(var key in this.searchForm){
          obj[key]=this.searchForm[key].trim()
          if(obj[key]!==""){
            count++;
          }
        }
        if(count === 0){
          this.$http.post("/php/chemotherapymodal/list.php",qs.stringify({
            page:this.pageNum,
            count:this.pageSize
          })).then(response=>{
            // console.log(response.data);
            this.totalNum = Number(response.data.total);
            this.tableData = response.data.list;
            this.tableData.forEach(item=>{
              item.VariantInfo = item.Symbol + ' ' + item.HGVSp;
            })
            this.tableData.VariantInfo = this.tableData.Symbol + '&' + this.tableData.HGVSp;
            this.loading = false;
          })
        }else{
          obj.page = this.pageNum;
          obj.count = this.pageSize;
          this.$http.post("/php/chemotherapymodal/search.php",qs.stringify(obj)).then(response=>{
            // console.log(response.data);
            this.totalNum = Number(response.data.total);
            this.tableData = response.data.list;
            this.tableData.forEach(item=>{
              item.VariantInfo = item.Symbol + ' ' + item.HGVSp;
            })
            this.tableData.VariantInfo = this.tableData.Symbol + '&' + this.tableData.HGVSp;
            this.loading = false;
          })
        }       
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chemoListcontainer{
    .tableColumnDisplay{
      display:hidden;
    }
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
}
</style>


