<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>变异管理</BreadcrumbItem>
      <BreadcrumbItem>SNP_InDel</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="Gene Symbol">
              <Input v-model="searchForm.Symbol" placeholder="请输入Gene Symbol"></Input>
          </FormItem>
          <FormItem label="染色体名称">
            <Input v-model="searchForm.ChrName" placeholder="请输入染色体编号"></Input>
          </FormItem>
          <FormItem label="染色体位置">
            <Input v-model="searchForm.Position" placeholder="请输入染色体位置"></Input>
          </FormItem>
          <!--<FormItem label="参考基因组序列">
            <Input v-model="searchForm.REF_base" placeholder="请输入参考基因组序列"></Input>
          </FormItem>
          <FormItem label="ALT序列">
            <Input v-model="searchForm.ALT_base" placeholder="请输入ALT序列"></Input>
          </FormItem>-->
          <FormItem label="HGVSp">
            <Input v-model="searchForm.HGVSp" placeholder="请输入HGVSp"></Input>
          </FormItem>
          <FormItem label="HGVSc">
            <Input v-model="searchForm.HGVSc" placeholder="请输入HGVSc"></Input>
          </FormItem>
          <FormItem label="Impact">
            <Input v-model="searchForm.Impact" placeholder="请输入Impact"></Input>
          </FormItem>
          <FormItem label="临床意义">
            <Input v-model="searchForm.Clin_Sig" placeholder="请输入临床意义"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" @click="addNewSNPInDel" size="large" icon="android-add">添加新变异</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border ref="selection" :loading='loading' :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" @click="batchRemove" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
import { sendChrID, reciveChrID } from '../../../../assets/switchChrName2ID.js'
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/vrsnpindel/index.php").then(response=>{
        // console.log(response.data);
        this.tableData = response.data.data;
        this.tableData.forEach(item=>{
          item.ChrName = reciveChrID(item.ChrID);
          item.ChromoNamePosition = item.ChrName + " & " + item.Position
          item.VariantType = "SNP_InDel"
        })
        this.totalNum = response.data.num;
        this.loading = false;
      })
    },
    data() {
      return {
        searchForm:{
          Symbol:'',
          ChrName:'',
          ChrID:'',
          Position:'',
          // REF_base:'',
          // ALT_base:'',
          HGVSp:'',
          HGVSc:'',
          Impact:'',
          Clin_Sig:'',
        },
        loading:true,
        totalNum:0,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"变异编号",
            key:'VariantID',
            align: 'center'
          },
          {
            title:"Gene Symbol",
            key:'Symbol',
            align: 'center'
          },
          {
            title:"染色体名称及位置",
            key:'ChromoNamePosition',
            align: 'center'
          },
          {
            title:"参考基因组序列",
            key:'REF_base',
            align: 'center'
          },
          {
            title:"ALT序列",
            key:'ALT_base',
            align: 'center'
          },
          {
            title:"HGVSp",
            key:'HGVSp',
            align: 'center'
          },
          {
            title:"HGVSc",
            key:'HGVSc',
            align: 'center'
          },
          {
            title:"变异类型",
            key:'VariantType',
            align: 'center'
          },
          {
            title:"Impact",
            key:'Impact',
            align: 'center'
          },
          {
            title:"临床意义",
            key:'Clin_Sig',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.VariantID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on:{
                    click:()=>{
                      this.edit(params.row.VariantID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.VariantID,params.index)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        totalData:[],
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.loading = true;
        this.pageSize = 10;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      },
      addNewSNPInDel(){
        this.$router.push("/basicdataModal/addnewsnpindel")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/browsnpindeldetail?VariantID=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/editsnpindeldetail?VariantID=" + id)
      },
      remove(id,index){
        this.loading = true;
        this.$http.post("/php/vrsnpindel/del.php",qs.stringify({
          VariantID:[id]
        })).then(response=>{
          // console.log(response.data);
          if(response.data){
            this.$Message.success("删除成功！正在刷新页面...")
            this.handlePage(this.pageNum)
          }else{
            this.$Message.error('删除失败！');
            this.loading = false;
          }
        })
      },
      batchRemove(){
        this.loading = true;
        let IDS = [];
        this.multipleSelection.forEach(item=>{
          IDS.push(item.VariantID)
        })
        this.$http.post("/php/vrsnpindel/del.php",qs.stringify({
          VariantID:IDS
        })).then(response=>{
          // console.log(response.data);
          if(response.data){
            this.$Message.success("批量删除成功！正在刷新页面...")
            this.handlePage(this.pageNum)
          }else{
            this.$Message.error('批量删除失败！');
            this.loading = false;
          }
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        if(this.searchForm.ChrName!=""){
          this.searchForm.ChrID = sendChrID(this.searchForm.ChrName);
        }else{
          this.searchForm.ChrID=""
        }
        let obj = {}
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
        }
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        this.$http.post("/php/vrsnpindel/index.php",qs.stringify(obj)).then(response=>{
          // console.log(response.data);
          this.tableData = response.data.data;
          this.tableData.forEach(item=>{
            item.ChrName = reciveChrID(item.ChrID);
            item.ChromoNamePosition = item.ChrName + "&" + item.Position
            item.VariantType = "SNP_InDel"
          })
          this.totalNum = response.data.num;
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.loading = true;
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


