<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/chromo">染色体管理</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="染色体名称">
              <Input v-model="searchForm.ChrName" placeholder="请输入染色体名称"></Input>
          </FormItem>
          <FormItem label="染色体长度">
            <Input v-model="searchForm.Length" placeholder="请输入染色体长度"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <!--<Button type="warning" size="large" icon="android-add" @click="addNewChromo">添加新染色体</Button>-->
      </div>
      <div class="chromoListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <!--<Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>-->
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalData.length" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/chromo/index.php").then((response)=>{
        //此接口未做分页功能，数据较少，直接返回全部数据
        // console.log(response.data);
        this.totalData = response.data;
        if(this.totalData.length<=this.pageSize){
          this.tableData = this.totalData;
        }else{
          this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
        }
        this.loading = false;
      })
    },
    data() {
      return {
        searchForm:{
          ChrName:'',
          Length:''
        },
        loading: true,
        tableHead:[
          // {
          //   type: 'selection',
          //   width: 60,
          //   align: 'center'
          // },
          {
            title:"染色体编号",
            key:'ChrID',
            align: 'center'
          },
          {
            title:"染色体名称",
            key:'ChrName',
            align: 'center'
          },
          {
            title:"染色体长度",
            key:'Length',
            align: 'center'
          },
          {
            title:"拼接版本",
            key:'AssemblyVersion',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center',
            "sortable": true
          },
          {
            title:"最后修改日期",
            key:'LatestUpdateDate',
            align: 'center',
            "sortable": true
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.ChrID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.ChrID)
                    }
                  }
                },''),
                // h('Button', {
                //   props: {
                //     type: 'text',
                //     size: 'large',
                //     icon: 'android-delete'
                //   },
                //   style: {
                //     padding: '5px'
                //   },
                //   on: {
                //     click: () => {
                //       this.remove(params.row.ChrID)
                //     }
                //   }
                // }, '')
              ]);
            }
          },
        ],
        tableData:[],
        totalData:[],
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.loading = true;
        let obj = {}
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
        }
        this.$http.post("/php/chromo/index.php",qs.stringify(obj)).then(response=>{
          // console.log(response.data);
          this.totalData = response.data;
          if(this.totalData.length<=this.pageSize){
            this.tableData = this.totalData;
          }else{
            this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
          }
          this.loading = false;
        })
      },
      addNewChromo(){
        this.$router.push("/basicdataModal/addnewchromo")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/browchromodetail?id=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/editchromodetail?id=" + id)
      },
      // remove(id){
      //   // console.log(id);        
      // },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.pageNum = val;
        this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
      },
      handlePageSize(val){
        this.pageSize = val;
        this.tableData = this.totalData.slice(this.pageSize*(this.pageNum-1),this.pageSize*this.pageNum);
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


