<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/clinicaltrial">临床试验</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="browContainer">
        <Form ref="browForm" :model="browForm" label-position="right" :label-width="100">
          <FormItem label="创建时间">
            <Input v-model="browForm.CreateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最新更新时间">
              <Input v-model="browForm.LatestUpdateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验编号">
              <Input v-model="browForm.NCTID" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验名称">
              <Input v-model="browForm.Title" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验分期">
            <Input v-model="browForm.Phase" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验状态">
              <Input v-model="browForm.Status" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="editSubmit">编辑</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
      <!--<transition name="component-fade" mode="out-in">
        <router-view></router-view>
      </transition>-->
      <div class="relatedContainer">
        <div class="deleteContainer">
          <Button type="warning" style="width: 120px" @click="addNewTherapy">添加新靶向治疗</Button>
        </div>
        <Table stripe border :loading="loading" ref="selection" height="200"  :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn" @click="batchRemove">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/targetedtherapymodal/updateListClinicalTrial.php",qs.stringify({
        ClinicalTrialID:this.clinicalTrialID
      })).then(response=>{
        // console.log(response.data);
        this.browForm = response.data.list[0]
      })
      this.$http.post("/php/targetedtherapymodal/clinicaltrialDetail.php",qs.stringify({
        ClinicalTrialID:this.clinicalTrialID
      })).then(response=>{
        // console.log(response.data);
        this.tableData = response.data.list
        this.totalNum = Number(response.data.total)
        this.loading=false;
      })
    },
    data() {
      return {
        clinicalTrialID:location.search.split("=")[1],
        browForm:{},
        loading:true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"Molecular Profile",
            key:'MolProfileName',
            align: 'center'
          },
          {
            title:"疾病信息",
            key:'DiseaseName_cn',
            align: 'center'
          },
          {
            title:"Response Type",
            key:'ResponseType',
            align: 'center'
          },
          {
            title:"疗法信息",
            key:'TherapyName',
            align: 'center'
          },
          {
            title:"证据等级",
            key:'EvidenceType',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            width:150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.TargetTreatmentID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.TargetTreatmentID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.TargetTreatmentID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[
          {
            molProfile:'111',
            disease:'111',
            resType:'11111111',
            treat:'11111111',
            evidence:'11111111',
            eviType:'11111111',
            reference:'11111111'
          }
        ],
        totalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      remove(){
        // console.log(id);
        this.$http.post("/php/targetedtherapymodal/delClinicalTrial.php",qs.stringify({
          ClinicalTrialID:[this.clinicalTrialID]
        })).then(response=>{
            // console.log(response.data);
            if(response.data){
              this.$Message.success("删除成功！即将跳转页面...")
              setTimeout(()=>{
                this.$router.push("/targetedtherapymodal/clinicaltrial")
              },1000)
            }else{
              this.$Message.error("删除失败！请稍后重试...")
            }
          })
      },
      editSubmit() {
        this.$router.push('/targetedtherapymodal/editclinicaltrialdetail?clinicaltrialid=' + this.clinicalTrialID)
      },
      backList(){
        this.$router.push('/targetedtherapymodal/clinicaltrial')
      },
      addNewTherapy(){
        this.$router.push('/targetedtherapymodal/addnewtargetedtherapy?clinicaltrialid=' + this.clinicalTrialID + '&nctid=' + this.browForm.NCTID)
      },
      show (id) {
        // console.log(id);
        this.$router.push("/targetedtherapymodal/browtargetedtherapydetail?targettreatmentid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/targetedtherapymodal/edittargetedtherapydetail?targettreatmentid=" + id)
      },
      remove(id){
        // console.log(id);
        // this.$http.post("/php/targetedtherapymodal/delClinicalTrial.php",qs.stringify({
        //   ClinicalTrialID:[id]
        // })).then(response=>{
        //     // console.log(response.data);
        //     if(response.data){
        //       this.$Message.success("删除成功！正在刷新页面...")
        //       this.multipleSelection=[];
        //       //刷新页面
        //       this.handlePage(this.pageNum);
        //     }else{
        //       this.$Message.error("删除失败！")
        //     }
        //     this.loading = false;
        //   })
      },
      batchRemove(){
        // console.log(id);
        let IDS = [];
        this.multipleSelection.forEach(item=>{
          IDS.push(item.ClinicalTrialID)
        })
        // this.$http.post("/php/targetedtherapymodal/delClinicalTrial.php",qs.stringify({
        //   ClinicalTrialID:IDS
        // })).then(response=>{
        //     console.log(response.data);
        //     if(response.data){
        //       this.$Message.success("批量删除成功！正在刷新页面...")
        //       this.multipleSelection=[];
        //       //刷新页面
        //       this.handlePage(this.pageNum);
        //     }else{
        //       this.$Message.error("批量删除失败！")
        //     }
        //   })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        this.$http.post("/php/targetedtherapymodal/clinicaltrialDetail.php",qs.stringify({
          ClinicalTrialID:this.clinicalTrialID
        })).then(response=>{
          // console.log(response.data);
          this.tableData = response.data.list
          this.loading=false;
        })   
      },
      handlePageSize(val){
        this.loading = true;
        this.pageSize = val;
        this.pageNum = 1;
        this.$http.post("/php/targetedtherapymodal/clinicaltrialDetail.php",qs.stringify({
          ClinicalTrialID:this.clinicalTrialID
        })).then(response=>{
          // console.log(response.data);
          this.tableData = response.data.list
          this.loading=false;
        })
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .browContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .relatedContainer{
    border-top:1px dotted #ccc;
    .batchContainer{
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
}
</style>


