//         this.$refs.menu.updateOpened();  
//         this.$refs.menu.updateActiveName();  
//       });  
//     } 


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



