<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>基因管理</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="Gene Symbol">
            <Input v-model="searchForm.Symbol" placeholder="请输入Gene Symbol"></Input>
          </FormItem>
          <FormItem label="基因名称">
            <Input v-model="searchForm.GeneName" placeholder="请输入基因名称"></Input>
          </FormItem>
          <FormItem label="染色体名称">
            <Input v-model="searchForm.ChrName" placeholder="请输入染色体名称"></Input>
          </FormItem>
          <FormItem label="物理图谱位置">
            <Input v-model="searchForm.Location" placeholder="请输入物理图谱位置"></Input>
          </FormItem>
          <FormItem label="创建日期">
            <DatePicker type="date" @on-change="getCreateTime" :options="dateOption" placeholder="请选择创建日期" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem label="最后修改日期">
            <DatePicker type="date" @on-change="getUpdateTime" :options="dateOption" placeholder="请选择最后修改日期" style="width: 162px"></DatePicker>
          </FormItem>
          <FormItem label="功能描述">
            <Input v-model="searchForm.Synonyms" placeholder="请输入功能描述"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" size="large" icon="android-add" @click="addNewGene">添加新基因</Button>
      </div>
      <div class="geneListcontainer">
        <Table stripe border :loading='loading' ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <!--<Button type="text" size="large" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>-->
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
import { sendChrID, reciveChrID } from '../../../assets/switchChrName2ID.js'
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/gene/index.php").then(response=>{
        // console.log( response.data.data);
        this.tableData = response.data.data;
        this.tableData.forEach(item=>{
          item.ChrName = reciveChrID(item.ChrID);
        })
        this.totalNum = response.data.num;
        this.loading = false;
      })
    },
    data() {
      return {
        searchForm:{
          GeneName:'',
          Symbol:'',
          ChrName:'',
          Location:'',
          CreateDate:'',
          LatestUpdateDate:'',
          Synonyms:''
        },
        dateOption:{
          disabledDate (date) {
            return date && date.valueOf() > Date.now();
          }
        },
        loading: true,
        tableHead:[
          // {
          //   type: 'selection',
          //   width: 60,
          //   align: 'center'
          // },
          {
            title:"基因编号",
            key:'GeneID',
            align: 'center',
            width: 100,
            className:'tableColumnDisplay'
          },
          {
            title:"Gene Symbol",
            key:'Symbol',
            align: 'center'
          },
          {
            title:"基因名称",
            key:'GeneName',
            align: 'center'
          },
          {
            title:"染色体名称",
            key:'ChrName',
            align: 'center'
          },
          {
            title:"物理图谱位置",
            key:'Location',
            align: 'center'
          },
          {
            title:"功能描述",
            key:'Synonyms',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后修改日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.GeneID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.GeneID)
                    }
                  }
                },''),
                // h('Button', {
                //   props: {
                //     type: 'text',
                //     size: 'large',
                //     icon: 'android-delete'
                //   },
                //   style: {
                //     padding: '5px'
                //   },
                //   on: {
                //     click: () => {
                //       this.remove(params.index)
                //     }
                //   }
                // }, '')
              ]);
            }
          },
        ],
        tableData:[],
        totalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      getCreateTime: function (startTime) {
        // console.log(startTime)
        this.searchForm.CreateDate = startTime
      },
      getUpdateTime: function (endTime) {
        this.searchForm.LatestUpdateDate = endTime
      },
      handleSubmit(name) {
        this.loading = true;
        this.pageSize = 10;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      },
      addNewGene(){
        this.$router.push("/basicdataModal/addnewgene")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/browgenedetail?geneid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/editgenedetail?geneid=" + id)
      },
      // remove(id){
      //   // console.log(id);        
      // },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        if(this.searchForm.ChrName!=""){
          this.searchForm.ChrID = sendChrID(this.searchForm.ChrName);
        }else{
          this.searchForm.ChrID=""
        }
        let obj = {}
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
        }
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        this.$http.post("/php/gene/index.php",qs.stringify(obj)).then(response=>{
          // console.log(response.data);
          this.tableData = response.data.data;
          this.tableData.forEach(item=>{
            item.ChrName = reciveChrID(item.ChrID);
          })
          this.totalNum = response.data.num;
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.loading = true;
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .geneListcontainer{
    .tableColumnDisplay{
      display:hidden;
    }
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
    
  }
  
}
</style>


