<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>疾病管理</BreadcrumbItem>
      <BreadcrumbItem>疾病路径</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="疾病英文名称">
            <Input v-model="searchForm.DiseaseName_en" placeholder="请输入疾病英文名称"></Input>
          </FormItem>
          <FormItem label="疾病中文名称">
            <Input v-model="searchForm.DiseaseName_cn" placeholder="请输入疾病中文名称"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" @click="addNewDiseasePath" size="large" icon="android-add">添加新疾病路径</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border ref="selection" :loading="loading" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn" @click="batchRemove">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/disease/diseasepath/").then(response=>{
        // console.log(response.data);
        this.tableData = response.data.data;
        this.totalNum = response.data.num;
        this.loading = false;
      })
    },
    data() {
      return {
        searchForm:{
          DiseaseName_en:'',
          DiseaseName_cn:''
        },
        loading:true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"疾病关联编号",
            key:'ChildParentID',
            align: 'center'
          },
          {
            title:"子疾病编号",
            key:'ChildDiseaseID',
            align: 'center'
          },
          {
            title:"子疾病英文名称",
            key:'ChildName_en',
            align: 'center'
          },
          {
            title:"子疾病中文名称",
            key:'ChildName_cn',
            align: 'center'
          },
          {
            title:"父疾病编号",
            key:'ParentDiseaseID',
            align: 'center'
          },
          {
            title:"父疾病英文名称",
            key:'ParentName_en',
            align: 'center'
          },
          {
            title:"父疾病中文名称",
            key:'ParentName_cn',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.ChildParentID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on:{
                    click: () => {
                      this.edit(params.row.ChildParentID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.ChildParentID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        totalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.loading = true;
        this.pageNum = 1;
        this.pageSize = 10;
        let obj = {};
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
        }
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        this.$http.post("/php/disease/diseasepath/query.php",qs.stringify(obj)).then(response=>{
          // console.log(response.data);
          this.tableData = response.data.data;
          this.totalNum = response.data.num;
          this.loading = false;
        })
      },
      addNewDiseasePath(){
        this.$router.push("/basicdataModal/addnewdiseasepath")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/browdiseasepathdetail?childparentid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/editdiseasepathdetail?childparentid=" + id)
      },
      remove(id){
        // console.log(id);
        this.loading = true;
        this.$http.post("/php/disease/diseasepath/del.php",qs.stringify({
          ChildParentID:[id]
        })).then(response=>{
          // console.log(response.data);
          if(response.data){
            this.$Message.success("删除成功！正在刷新页面...")
            this.handlePage(this.pageNum)
          }else{
            this.$Message.error('删除失败！');
            this.loading = false;
          }
        })
      },
      batchRemove(){
        this.loading = true;
        let IDS = [];
        this.multipleSelection.forEach(item=>{
          IDS.push(item.ChildParentID)
        })
        this.$http.post("/php/disease/diseasepath/del.php",qs.stringify({
          ChildParentID:IDS
        })).then(response=>{
          console.log(response.data);
          if(response.data){
            this.$Message.success("批量删除成功！正在刷新页面...")
            this.handlePage(this.pageNum)
          }else{
            this.$Message.error("批量删除失败！")
            this.loading = false;
          }
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        let obj = {};
        let count = 0;
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
          if(this.searchForm[key].trim()!==''){
            count++;
          }
        }
        let url = count===0?'/php/disease/diseasepath/index.php':'/php/disease/diseasepath/query.php';
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        this.$http.post(url,qs.stringify(obj)).then(response=>{
          // console.log(response.data);
          this.tableData = response.data.data;
          this.totalNum = response.data.num;
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.loading = true;
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


