<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/chemotherapymodal/chemotherapy">化学药物治疗</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='browchemotherapydetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='editchemotherapydetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="remove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="变异位点" prop="VariantInfo">
            <Input v-model="editForm.VariantInfo" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="基因型" prop="Genotype">
              <Input v-model="editForm.Genotype" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病编号" prop="DiseaseID">
            <AutoComplete v-model="editForm.DiseaseID" @on-search="searchDiseaseID" @on-select="selectDiseaseID" placeholder="请选择疾病编号" style="width:280px">
              <Option v-for="(item, index) in DiseaseArr" :value="item.DiseaseID" :key="index">{{ item.DiseaseID }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="疾病中文名称">
              <AutoComplete v-model="editForm.DiseaseName_cn" @on-search="searchDiseaseName" @on-select="selectDiseaseName" placeholder="请选择疾病中文名称" style="width:280px">
              <Option v-for="(item, index) in DiseaseArr" :value="item.DiseaseName_cn" :key="index">{{ item.DiseaseName_cn }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="药物编号" prop="DrugID">
            <AutoComplete v-model="editForm.DrugID" @on-search="searchDrugID" @on-select="selectDrugID" placeholder="请选择药物编号" style="width:280px">
            <Option v-for="(item, index) in DrugArr" :value="item.DrugID" :key="index">{{ item.DrugID }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="药物中文名称">
            <AutoComplete v-model="editForm.DrugName_cn" @on-search="searchDrugName" @on-select="selectDrugName" placeholder="请选择药物中文名称" style="width:280px">
              <Option v-for="(item, index) in DrugArr" :value="item.DrugName_cn" :key="index">{{ item.DrugName_cn }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="英文功能描述">
              <Input v-model="editForm.Synonyms" type="textarea" :rows="4" style="width: 280px" :disabled="this.currentPath==='browchemotherapydetail'"></Input>
              <Button type="dashed" shape="circle" style="width:120px">添加至分配列表</Button>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="editSubmit" v-if="currentPath==='browchemotherapydetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath==='editchemotherapydetail'">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/chemotherapymodal/updateList.php",qs.stringify({
        ChemtreaTmentID:this.chemoID
      })).then(response=>{
        // console.log(response.data.list);
        this.editForm = response.data.list[0];
        this.editForm.VariantInfo = this.editForm.Symbol + '&' + this.editForm.HGVSp;
      })
      this.$http.post("/php/chemotherapymodal/getDiseaseNum.php",qs.stringify({
        DiseaseID:'',
        DiseaseName_cn:''
      })).then(response=>{
          // console.log(response.data);
          this.DiseaseArr = response.data;
      })
      this.$http.post("/php/chemotherapymodal/getDrugNum.php",qs.stringify({
        DrugID:'',
        DrugName_cn:''
      })).then(response=>{
          // console.log(response.data);
          this.DrugArr = response.data;
      })
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        chemoID:location.search.split("=")[1],
        editForm:{},
        DiseaseArr:[],
        DrugArr:[],
        rule:{
          VariantInfo:[{
            required:true,message:'不能为空',trigger:'blur,change'
          }],
          Genotype:[{
            required:true,message:'不能为空',trigger:'blur,change'
          }],
          DiseaseID:[{
            required:true,message:'不能为空',trigger:'blur,change'
          }],
          DrugID:[{
            required:true,message:'不能为空',trigger:'blur,change'
          }]
        }
      }
    },
    methods: {
      remove(){
        // console.log(id);
        this.$http.post("/php/chemotherapymodal/del.php",qs.stringify({
          ChemtreaTmentID:[this.chemoID]
        })).then(response=>{
          if(response.data){
            this.$Message.success('删除成功！即将跳转页面...');
            setTimeout(()=>{
              this.$router.push('/chemotherapymodal/chemotherapy')   
            },1000)
          }else{
            this.$Message.error('删除失败！');
          }
        })
      },
      editSubmit(){
        this.currentPath = 'editchemotherapydetail'
      },
      searchDiseaseID(value){
        this.$http.post("/php/chemotherapymodal/getDiseaseNum.php",qs.stringify({
          DiseaseID:value.trim()
        })).then(response=>{
          // console.log(response.data);
          this.DiseaseArr = response.data
        })
      },
      selectDiseaseID(value){
        // console.log(value);
        this.editForm.DiseaseID = value;
        this.editForm.DiseaseName_cn = this.DiseaseArr.filter(item=>{
          return item.DiseaseID.indexOf(value) !== -1
        })[0].DiseaseName_cn;
      },
      searchDiseaseName(value){
        this.$http.post("/php/chemotherapymodal/getDiseaseNum.php",qs.stringify({
          DiseaseName_cn:value.trim()
        })).then(response=>{
          // console.log(response.data);
          this.DiseaseArr = response.data
          // console.log(this.DiseaseArr);
        })
      },
      selectDiseaseName(value){
        // console.log(value);
        this.editForm.DiseaseName_cn = value;
        this.editForm.DiseaseID = this.DiseaseArr.filter(item=>{
          return item.DiseaseName_cn.indexOf(value) !== -1
        })[0].DiseaseID;
      },
      searchDrugID(value){
        // console.log(value);
        this.$http.post("/php/chemotherapymodal/getDrugNum.php",qs.stringify({
          DrugID:value.trim()
        })).then(response=>{
          // console.log(response.data);
          this.DrugArr = response.data
        })
      },
      selectDrugID(value){
        // console.log(value);
        this.editForm.DrugID = value;
        this.editForm.DrugName_cn = this.DrugArr.filter(item=>{
          return item.DrugID.indexOf(value) !== -1
        })[0].DrugName_cn;
      },
      searchDrugName(value){
        this.$http.post("/php/chemotherapymodal/getDrugNum.php",qs.stringify({
          DrugName_cn:value.trim()
        })).then(response=>{
          // console.log(response.data);
          this.DrugArr = response.data
        })
      },
      selectDrugName(value){
        // console.log(value);
        this.editForm.DrugName_cn = value;
        this.editForm.DrugID = this.DrugArr.filter(item=>{
          return item.DrugName_cn.indexOf(value) !== -1
        })[0].DrugID;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {};
            let count=0;
            
            for(let key in this.editForm){
              obj[key]=this.editForm[key];
            }
            this.$http.post("/php/chemotherapymodal/edit.php",qs.stringify(obj)).then(response=>{
              // console.log(response.data);
              if(response.data){
                this.$Message.success('保存成功！即将跳转页面...');
                setTimeout(()=>{
                  this.$router.push('/chemotherapymodal/chemotherapy')   
                },1000)
              }else{
                this.$Message.error('保存失败！请检查页面输入内容');
              }
              
            })
          }
        })
      },
      backList(){
        this.$router.push("/chemotherapymodal/chemotherapy")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


