<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/disease">疾病信息</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='browdiseasedetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='editdiseasedetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="remove">删除此疾病</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
        <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病名称（英文）" prop="DiseaseName_en">
            <Input v-model="editForm.DiseaseName_en" :disabled="this.currentPath==='browdiseasedetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病名称（中文）">
              <Input v-model="editForm.DiseaseName_cn" :disabled="this.currentPath==='browdiseasedetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="ICD编号">
            <Input v-model="editForm.ICDID" :disabled="this.currentPath==='browdiseasedetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="疾病路径">
            <Input v-model="editForm.diseasePath" :disabled="this.currentPath==='browdiseasedetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后修改日期">
              <Input v-model="editForm.LatestUpdateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit('editForm')" v-if="this.currentPath==='browdiseasedetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editForm')" v-if="this.currentPath==='editdiseasedetail'">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/disease/index.php",qs.stringify({DiseaseID:this.diseaseID})).then(response=>{
        // console.log(response.data);
        if(response.data.data.length!==0){
          this.editForm = response.data.data[0]
        }else{
          this.$Message.error("该疾病不存在！即将跳转回列表页...")
          setTimeout(()=>{
            this.$router.push('/basicdataModal/disease')
          },1000)
        }
      })
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        diseaseID:location.search.split("=")[1],
        editForm:{},
        rule:{
          DiseaseName_en:[
            {
              required: true, message: '疾病名称（英文）不能为空', trigger: 'blur'
            }
          ],
        }
      }
    },
    methods: {
      handleEdit(name){
        this.currentPath = 'editdiseasedetail'
      },
      remove(){
        this.$http.post('/php/disease/del.php',qs.stringify({
          DiseaseID:[this.diseaseID]
        })).then(response=>{
          if(response.data==="wrong"){
            this.$Message.error('该疾病已在其他地方被引用，无法删除！');
            return false;
          }
          if(response.data){
            this.$Message.success("删除成功！即将跳转页面...")
            setTimeout(()=>{
              this.$router.push('/basicdataModal/disease')
            },1000)
          }else{
            this.$Message.error('删除失败');
          }
        })
      },
      handleSubmit(name) {
        let obj = {};
        for(var key in this.editForm){
          obj[key] = this.editForm[key].trim()
        }
        this.$http.post('/php/disease/edit.php',qs.stringify(obj)).then(response=>{
          if(response.data){
            this.$Message.success('保存成功，即将跳转页面...');
            setTimeout(()=>{
              this.$router.push('/basicdataModal/disease')
            },1000)
          }else{
            this.$Message.error('保存失败，请检查输入的内容');
          }
        })
      },
      backList() {
        this.$router.push('/basicdataModal/disease')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


