<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/chromo">文献信息</BreadcrumbItem>
      <BreadcrumbItem>添加新的文献</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的文献</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的文献</p>
        <Form ref="addForm" :model="addForm" label-position="right" :label-width="100">
          <FormItem label="作者信息">
              <Input v-model="addForm.authorInfo" placeholder="请输入作者" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="文献标题">
            <Input v-model="addForm.refTitle" placeholder="请输入文献标题" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Pubmed ID">
              <Input v-model="addForm.pubmedID" placeholder="请输入Pubmed ID" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="杂志">
              <Input v-model="addForm.magazine" placeholder="请输入杂志" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Issue">
              <Input v-model="addForm.issue" placeholder="请输入Issue" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Volume">
              <Input v-model="addForm.volume" placeholder="请输入Volume" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="年份">
              <Input v-model="addForm.publicYear" placeholder="请输入年份" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="页码">
              <Input v-model="addForm.pageNum" placeholder="请输入页码" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="handleSubmit('addForm')">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          authorInfo:'',
          refTitle:'',
          pubmedID:'',
          magazine:'',
          issue:'',
          volume:'',
          publicYear:'',
          pageNum:''
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$router.push('/basicdataModal/reference')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


