<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/targetedtherapy2">靶向治疗疗法</BreadcrumbItem>
      <BreadcrumbItem>查看</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="remove">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="browForm" :model="browForm" label-position="right" :label-width="120">
          <FormItem label="创建时间">
            <Input v-model="browForm.CreateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最新更新时间">
              <Input v-model="browForm.LatestUpdateDate" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem label="靶向治疗疗法名称">
            <Input v-model="browForm.TherapyName" readonly style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="editSubmit">编辑</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
      <transition name="component-fade" mode="out-in">
        <router-view></router-view>
      </transition>
    </Content>
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/targetedtherapy2/edit.php",qs.stringify({
        TargetTreatmentTherapyID:this.targetTreatmentTherapyID
      })).then(response=>{
        // console.log(response.data);
        this.browForm = response.data[0]
      })
    },
    data() {
      return {
        targetTreatmentTherapyID:location.search.split('=')[1],
        browForm:{
          TherapyName:'',
          CreateDate:'',
          LatestUpdateDate:''
        }
      }
    },
    methods: {
      remove(){
        // console.log(id);
        this.$http.post("/php/targetedtherapy2/del.php",qs.stringify({
          TargetTreatmentTherapyID:[this.targetTreatmentTherapyID]
        })).then(response=>{
          // console.log(response.data);
          if(response.data){
            if(response.data){
              this.$Message.success('删除成功！页面正在跳转...');
              setTimeout(()=>{
                this.$router.push("/targetedtherapymodal/targetedtherapy2")
              },1000)
            }else{
              this.$Message.error('删除失败！');
            }
          }
        }) 
      },
      editSubmit(){
        this.$router.push("/targetedtherapymodal/edittargetedtherapy2detail?therapy2=" + this.targetTreatmentTherapyID)
      },
      backList(){
        this.$router.push("/targetedtherapymodal/targetedtherapy2")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
  .editContainer{
    border-bottom:1px dotted #ccc;
  }
}
</style>


