<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/gene">基因管理</BreadcrumbItem>
      <BreadcrumbItem>编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <!--<Button type="warning">删除此基因</Button>-->
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="Gene Symbol" prop="Symbol">
            <Input v-model="editForm.Symbol" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="基因名称" prop="GeneName">
              <Input v-model="editForm.GeneName" :disabled="this.currentPath==='browchromodetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="染色体名称" prop="ChrName">
            <Input v-model="editForm.ChrName" :disabled="this.currentPath==='browchromodetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="物理图谱位置">
              <Input v-model="editForm.Location" :disabled="this.currentPath==='browchromodetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="英文功能描述">
              <Input v-model="editForm.Synonyms" type="textarea" :rows="4" placeholder="请输入英文功能描述" style="width: 280px"></Input>
              <Button type="dashed" shape="circle" style="width:120px">添加至分配列表</Button>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('editForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
import { sendChrID, reciveChrID } from '../../../assets/switchChrName2ID.js'
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/gene/",qs.stringify({GeneID:this.geneID})).then(response=>{
        // console.log(response.data);
        this.editForm = response.data.data[0];
        if(Number(this.editForm.ChrID)<23){
          this.editForm.ChrName = "chr" + this.editForm.ChrID
        }else if(this.editForm.ChrID==='23'){
          this.editForm.ChrName = "chrX"
        }else if(this.editForm.ChrID==='24'){
          this.editForm.ChrName = "chrY"
        }else if(this.editForm.ChrID==='25'){
          this.editForm.ChrName = "chrM"
        }
      })
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        geneID:location.search.split("=")[1],
        editForm:{},
        rule:{
          Symbol:[
            {
              required: true, message: 'Gene Symbol不能为空', trigger: 'blur'
            }
          ],
          GeneName:[
            {
              required: true, message: '基因名称不能为空', trigger: 'blur'
            }
          ],
          ChrName:[
            {
              required: true, message: '染色体名称不能为空', trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        let obj = this.editForm;
        obj.ChrID = sendChrID(obj.ChrName);
        if(obj.ChrID==="error"){
          this.$Message.error('染色体名称错误，请重新输入');
        }else{
          this.$refs[name].validate((valid) => {
            if (valid) {
              this.$http.post("/php/gene/edit.php",qs.stringify(obj)).then(response=>{
                // console.log(response.data);
                if(response.data){
                  this.$Message.success('提交成功，即将跳转页面...');
                  setTimeout(()=>{
                  this.$router.push('/basicdatamodal/gene')              
                  },1000)
                }else{
                  this.$Message.error('提交失败，请检查输入的内容');
                }
              })
            }
          })
        }
        
      },
      backList(){
        this.$router.push("/basicdatamodal/gene")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


