        // {
        //   path: 'reference',
        //   component: Reference
        // },
        // {
        //   path: 'addnewreference',
        //   component: AddNewReference
        // },
        // {
        //   path: 'browrefdetail',
        //   component: EditRefDetail
        // },
        // {
        //   path: 'editrefdetail',
        //   component: EditRefDetail
        // },
        // {
        //   path: 'author',
        //   component: Author
        // },
        // {
        //   path: 'addnewauthor',
        //   component: AddNewAuthor
        // },
        // {
        //   path: 'browauthordetail',
        //   component: EditAuthorDetail
        // },
        // {
