<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>疾病管理</BreadcrumbItem>
      <BreadcrumbItem>疾病信息</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="疾病编号">
            <Input v-model="searchForm.DiseaseID" placeholder="请输入疾病编号）"></Input>
          </FormItem>
          <FormItem label="疾病名称（英文）">
            <Input v-model="searchForm.DiseaseName_en" placeholder="请输入疾病名称（英文）"></Input>
          </FormItem>
          <FormItem label="疾病名称（中文）">
            <Input v-model="searchForm.DiseaseName_cn" placeholder="请输入疾病名称（中文）"></Input>
          </FormItem>
          <FormItem label="ICD-10编号">
            <Input v-model="searchForm.ICDID" placeholder="请输入ICD-10编号"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" @click="addNewDisease" size="large" icon="android-add">添加新疾病</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" @click="batchRemove" class="batchBtn">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/disease/index.php").then(response=>{
        console.log(response.data);
        this.tableData = response.data.data;
        this.totalNum = response.data.num;
        this.loading = false;
      })
    },
    data() {
      return {
        searchForm:{
          DiseaseID:'',
          DiseaseName_en:'',
          DiseaseName_cn:'',
          ICDID:''
        },
        loading:true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"疾病编号",
            key:'DiseaseID',
            align: 'center'
          },
          {
            title:"疾病英文名称",
            key:'DiseaseName_en',
            align: 'center'
          },
          {
            title:"疾病中文名称",
            key:'DiseaseName_cn',
            align: 'center'
          },
          {
            title:"ICD-10编号",
            key:'ICDID',
            align: 'center'
          },
          {
            title:"创建日期",
            key:'CreateDate',
            align: 'center'
          },
          {
            title:"最后修改日期",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.DiseaseID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.DiseaseID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.DiseaseID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        totalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.loading = true;
        this.pageNum=1;
        this.pageSize=10;
        let obj = {};
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
        }
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        this.$http.post('/php/disease/index.php',qs.stringify(obj)).then(response=>{
          console.log(response.data);
          this.tableData = response.data.data;
          this.totalNum = response.data.num;
          this.loading = false;
        })
      },
      addNewDisease(){
        this.$router.push("/basicdataModal/addnewdisease")
      },
      show (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/browdiseasedetail?diseaseid=" + id)
      },
      edit (id) {
        // console.log(id);
        this.$router.push("/basicdatamodal/editdiseasedetail?diseaseid=" + id)
      },
      remove(id){
        // console.log(id);
        this.loading = true;
        this.$http.post('/php/disease/del.php',qs.stringify({
          DiseaseID:[id]
        })).then(response=>{
          if(response.data==="wrong"){
            this.loading = false;
            this.$Message.error('该疾病已在其他地方被引用，无法删除！');
            return false;
          }
          if(response.data){
            this.$Message.success('删除成功！正在刷新页面...');
            this.handlePage(this.pageNum)
          }else{
            this.$Message.error('删除失败！');
            this.loading = false;
          }
        })
      },
      batchRemove(){
        this.loading = true;
        var IDS=[];
        this.multipleSelection.forEach(item=>{
          IDS.push(item.DiseaseID)
        })
        this.$http.post('/php/disease/del.php',qs.stringify({
          DiseaseID:IDS
        })).then(response=>{
          if(response.data){
            this.$Message.success("批量删除成功！正在刷新页面...")
            this.handlePage(this.pageNum)
          }else{
            this.$Message.error('批量删除失败！');
            this.loading = false;
          }
        })
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        let obj = {};
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim()
        }
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        this.$http.post('/php/disease/index.php',qs.stringify(obj)).then(response=>{
          // console.log(response.data);
          this.tableData = response.data.data;
          this.totalNum = response.data.num;
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.loading = true;
        this.pageSize = val;
        this.pageNum = 1;
        this.handlePage(this.pageNum)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


