<template>
  <div class="relatedContainer">
    <div class="editContainer">
      <p v-if="currentPath==='browvariantdetail'">查看变异信息</p>
      <p v-if="currentPath==='editvariantdetail'">修改变异信息（请按顺序依次填写并选择）</p>
      <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="100">
        <FormItem label="创建日期">
          <Input v-model="editForm.CreateDate" disabled style="width: 280px"></Input>
        </FormItem>
        <FormItem label="最后修改日期">
          <Input v-model="editForm.LatestUpdateDate" disabled style="width: 280px"></Input>
        </FormItem>
        <!--<FormItem label="变异编号">
          <Input v-model="editForm.MolProfileVariantsID" disabled style="width: 280px"></Input>
        </FormItem>-->
        <FormItem label="Gene Symbol" prop="Symbol">
          <AutoComplete v-model="editForm.Symbol" :autofocus="step1" :disabled="step1" @on-search="searchSymbol" @on-select="selectSymbol" placeholder="请选择Gene Symbol" style="width:280px">
            <Option v-for="(item, index) in geneSymbolData" :value="item" :key="index">{{ item }}</Option>
          </AutoComplete>
        </FormItem>
        <FormItem label="HGVSp" prop="HGVSp">
          <AutoComplete v-model="editForm.HGVSp" :disabled="step2" @on-search="searchhgvsp" @on-select="selecthgvsp" placeholder="请选择HGVSp" style="width:280px">
            <Option v-for="(item, index) in hgvspData" :value="item.HGVSp" :key="index">{{ item.HGVSp }}</Option>
          </AutoComplete>
        </FormItem>
        <FormItem label="HGVSc" prop="HGVSc">
          <AutoComplete v-model="editForm.HGVSc" :disabled="step3" @on-search="searchhgvsc" @on-select="selecthgvsc" placeholder="请选择HGVSc" style="width:280px">
            <Option v-for="(item, index) in hgvscData" :value="item.HGVSc" :key="index">{{ item.HGVSc }}</Option>
          </AutoComplete>
        </FormItem>
        <FormItem :label-width="10">
          <Button type="primary" @click="handleEdit" v-if="currentPath==='browvariantdetail'">编辑</Button>
          <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath==='editvariantdetail'">保存</Button>
          <Button @click="backList">返回</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let search = location.search.split("?")[1];
      this.molProfileID = search.split("&")[0].split("=")[1]
      this.molProfileVariantsID = search.split("&")[1].split("=")[1]
      if(this.currentPath==='editvariantdetail'){
        this.step1=false;
      }
        this.$http.all([
          this.$http.post("/php/targetedtherapymodal/editVariantDetail.php",qs.stringify({MolProfileVariantsID:this.molProfileVariantsID,})),
          this.$http.post("/php/gsymbol/index.php"),
          this.$http.post("/php/targetedtherapymodal/getHgvsp.php",qs.stringify({Symbol:this.editForm.Symbol}))
        ]).then(this.$http.spread((profile,symbol,hgvsp)=>{
          this.editForm=profile.data.list[0];
          this.geneSymbolArr = symbol.data;
          this.geneSymbolData = this.geneSymbolArr;
          this.hgvspArr = hgvsp.data.list;
          this.hgvspData = this.hgvspArr;
        }))
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[3],
        molProfileID:'',
        molProfileVariantsID:'',
        step1:true,
        step2:true,
        step3:true,
        geneSymbolArr:[],
        geneSymbolData:[],
        hgvspArr:[],
        hgvspData:[],
        hgvscArr:[],
        hgvscData:[],
        deleteModal:false,
        editForm:{},
        rule:{
          Symbol:[
            {
              required: true, message: '请选择Gene Symbol', trigger: 'blur,change'
            }
          ],
          HGVSp:[
            {
              required: true, message: '请选择Gene Symbol', trigger: 'blur,change'
            }
          ],
          HGVSc:[
            {
              required: true, message: '请选择Gene Symbol', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      handleEdit(){
        this.currentPath = 'editvariantdetail';
        this.step1 = false;
      },
      searchSymbol(value){
        this.geneSymbolData = this.geneSymbolArr.filter(item=>{
          return item.toUpperCase().indexOf(value.toUpperCase()) !== -1
        })
      },
      selectSymbol(value){
        // console.log(value);
        this.editForm.Symbol = value;
        this.$http.post("/php/targetedtherapymodal/getHgvsp.php",qs.stringify({
          Symbol:value
        })).then(response=>{
          // console.log(response.data);
          this.hgvspArr = response.data.list;
          this.step1 = true;
          this.step2 = false;
          this.step3 = true;
          this.editForm.HGVSp = 'p.'
        })
      },
      searchhgvsp(value){
        this.hgvspData = this.hgvspArr.filter(item=>{
          return item.HGVSp.indexOf(value) !== -1
        })
      },
      selecthgvsp(value){
        this.editForm.HGVSp = value;    
        this.hgvscArr = this.hgvspData.filter(item=>{
          return item.HGVSp===value
        })
        this.step1 = true;
        this.step2 = true;
        this.step3 = false;
        this.editForm.HGVSc = 'c.'
      },
      searchhgvsc(value){
        this.hgvscData = this.hgvscArr.filter(item=>{
          return item.HGVSc.indexOf(value) !== -1
        })
      },
      selecthgvsc(value){
        this.editForm.HGVSc = value;
        this.hgvscData = this.hgvscArr.filter(item=>{
          return item.HGVSc===value;
        })
        this.editForm.VariantID = this.hgvscData[0].VariantID;
        this.step1 = true;
        this.step2 = true;
        this.step3 = true;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$http.post("/php/targetedtherapymodal/updateVariantProfile.php",qs.stringify({
              MolProfileVariantsID:this.editForm.MolProfileVariantsID,
              VariantID:this.editForm.VariantID
            })).then(response=>{
              // console.log(response.data);
              if(response.data==='wrong'){
                this.$Message.warning('此变异在该分子谱中已存在，请重新输入');
                return false;
              }
              if(response.data){
                this.$Message.success('保存成功！页面正在跳转...');
                setTimeout(()=>{
                  this.$router.push("/targetedtherapymodal/browvariantprofiledetail/variantlist?molprofileid=" + this.molProfileID)
                },1000)
              }else{
                this.$Message.error('保存失败！');
              }
            })
          } 
        })
      },
      backList() {
        this.$router.push("/targetedtherapymodal/browvariantprofiledetail/variantlist?molprofileid=" + this.molProfileID)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.relatedContainer{
  padding-top:20px;
  .editContainer{
    padding:5px 10px;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
}
</style>


