<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/drugclass">药物类别</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='browdrugclassdetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='editdrugclassdetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="remove">删除此药物</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="left" :label-width="100">
          <FormItem label="创建日期">
              <Input v-model="editForm.CreateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后更新日期">
              <Input v-model="editForm.LatestUpdateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物类别编号" prop="DrugClassID">
              <Input v-model="editForm.DrugClassID" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物类别名称（英文）" prop="DrugClassName_en">
            <Input v-model="editForm.DrugClassName_en" :disabled="this.currentPath==='browdrugclassdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物类别名称（中文）">
              <Input v-model="editForm.DrugClassName_cn" :disabled="this.currentPath==='browdrugclassdetail'" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit('editForm')" v-if="this.currentPath==='browdrugclassdetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editForm')" v-if="this.currentPath==='editdrugclassdetail'">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/drug/drugclass/",qs.stringify({
        DrugClassID:this.drugClassID
      })).then(response=>{
        // console.log(response.data);
        this.editForm = response.data.data[0]
      })
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        drugClassID:location.search.split("=")[1],
        editForm:{},
        rule:{
          DrugClassID:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ],
          DrugClassName_en:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      remove(){
        this.$http.post("/php/drug/drugclass/del.php",qs.stringify({
          DrugClassID:[this.drugClassID]
        })).then(response=>{
          // console.log(response.data);
          if(response.data==="wrong"){
            this.$Message.error("该药物类别已被引用，无法删除！")
            return false;
          }
          if(response.data){
            this.$Message.success("删除成功！即将跳转页面...")
            setTimeout(()=>{
              this.$router.push('/basicdataModal/drugclass')
            },1000)
          }else{
            this.$Message.error("删除失败！")
          }
        })
      },
      handleEdit(name){
        this.currentPath = "editdrugclassdetail"
      },
      handleSubmit(name) {
        // this.$router.push('/basicdataModal/drugclass')
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {};
            for(var key in this.editForm){
              obj[key] = this.editForm[key].trim()
            }
            // console.log(obj);
            this.$http.post("/php/drug/drugclass/edit.php",qs.stringify(obj)).then(response=>{
              console.log(response.data);
              if(response.data){
                this.$Message.success("保存成功，即将跳转页面...")
                setTimeout(()=>{
                  this.$router.push('/basicdataModal/drugclass')
                },1000)
              }else{
                this.$Message.error('保存失败，请检查输入的内容');
              }
            })
          }
        })
      },
      backList(){
        this.$router.push('/basicdataModal/drugclass')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


