<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/clinicaltrial">临床试验</BreadcrumbItem>
      <BreadcrumbItem>添加新的临床试验</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的临床试验</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的临床试验</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="临床试验编号" prop="NCTID">
              <Input v-model="addForm.NCTID" placeholder="请输入临床试验编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验名称" prop="Title">
              <Input v-model="addForm.Title" placeholder="请输入临床试验名称" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验分期" prop="Phase">
            <Input v-model="addForm.Phase" placeholder="请输入临床试验分期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床试验状态" prop="Status">
              <Input v-model="addForm.Status" placeholder="请输入临床试验状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          NCTID:'',
          Title:'',
          Phase:'',
          Status:''
        },
        rule:{
          NCTID:[
            {
              required:true,message:'不能为空',trigger:'blue,change'
            }
          ],
          Title:[
            {
              required:true,message:'不能为空',trigger:'blue,change'
            }
          ],
          Phase:[
            {
              required:true,message:'不能为空',trigger:'blue,change'
            }
          ],
          Status:[
            {
              required:true,message:'不能为空',trigger:'blue,change'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {};
            for(var key in this.addForm){
              obj[key] = this.addForm[key].trim()
            }
            this.$http.post("/php/targetedtherapymodal/addNewClinicalTrial.php",qs.stringify(obj)).then(response=>{
              // console.log(response.data);
              if(response.data){
                this.$Message.success('保存成功，即将跳转页面...');
                setTimeout(()=>{
                  this.$router.push("/targetedtherapymodal/clinicaltrial")
                },1000)
              }else{
                this.$Message.error('保存失败,请检查输入内容');
              }
            })
          } else {
            this.$Message.error('Fail!');
          }
        })
      },
      backList(){
        this.$router.push("/targetedtherapymodal/clinicaltrial")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


