<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/drug">药物信息</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='browdrugdetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="this.currentPath==='editdrugdetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning" @click="remove">删除此药物</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="left" :label-width="100">
          <FormItem label="创建日期">
            <Input v-model="editForm.CreateDate" disabled placeholder="请选择创建日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最后修改日期">
            <Input v-model="editForm.LatestUpdateDate" disabled placeholder="请选择最后修改日期" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物编号" prop="DrugID">
            <Input v-model="editForm.DrugID" disabled placeholder="请输入药物编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物名称（英文）" prop="DrugName_en">
            <Input v-model="editForm.DrugName_en" :disabled="this.currentPath==='browdrugdetail'" placeholder="请输入药物名称（英文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="药物名称（中文）">
            <Input v-model="editForm.DrugName_cn" :disabled="this.currentPath==='browdrugdetail'" placeholder="请输入药物名称（中文）" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="FDA药物状态">
            <Input v-model="editForm.FDAStatus" :disabled="this.currentPath==='browdrugdetail'" placeholder="请输入FDA药物状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="CFDA药物状态">
            <Input v-model="editForm.CFDAStatus" :disabled="this.currentPath==='browdrugdetail'" placeholder="请输入CFDA药物状态" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleEdit('editForm')" v-if="this.currentPath==='browdrugdetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editForm')" v-if="this.currentPath==='editdrugdetail'">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/drug/index.php",qs.stringify({
        DrugID:this.drugID
      })).then(response=>{
        // console.log(response.data);
        this.editForm = response.data.data[0]
      })
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        drugID:location.search.split("=")[1],
        editForm:{},
        rule:{
          DrugID:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ],
          DrugName_en:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      remove(){
        this.$http.post("/php/drug/del.php",qs.stringify({
          DrugID:[this.drugID]
        })).then(response=>{
          // console.log(response.data);
          if(response.data==="wrong"){
            this.$Message.error("该药物已被引用，无法删除！")
            return false;
          }
          if(response.data){
            this.$Message.success("删除成功！即将跳转页面...")
            setTimeout(()=>{
              this.$router.push('/basicdataModal/drug')
            },1000)
          }else{
            this.$Message.error("删除失败！")
          }
        })
      },
      handleEdit(name){
        this.currentPath = 'editdrugdetail'
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {};
            for(var key in this.editForm){
              obj[key] = this.editForm[key].trim()
            }
            this.$http.post("/php/drug/edit.php",qs.stringify(obj)).then(response=>{
              // console.log(response.data);
              if(response.data){
                this.$Message.success("保存成功，即将跳转页面...")
                setTimeout(()=>{
                  this.$router.push('/basicdataModal/drug')
                },1000)
              }else{
                this.$Message.error('保存失败，请检查输入的内容');
              }
            })
          }
        })
      },
      backList(){
        this.$router.push('/basicdataModal/drug')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


