<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/targetedtherapy">靶向治疗</BreadcrumbItem>
      <BreadcrumbItem>添加新的靶向治疗</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的靶向治疗</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的靶向治疗</p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="临床实验编号" prop="NCTID">
            <Input v-model="addForm.NCTID" disabled placeholder="请输入临床编号" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="分子谱名称" prop="MolProfileName">
            <AutoComplete v-model="addForm.MolProfileName" autofocus @on-search="searchProfile" @on-select="selectProfile" placeholder="请输入分子谱名称" style="width:280px">
              <Option v-for="(item, index) in molProfileData" :value="item.MolProfileName" :key="index">{{ item.MolProfileName }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="疾病中文名称" prop="DiseaseName_cn">
            <AutoComplete v-model="addForm.DiseaseName_cn" @on-search="searchDisease" @on-select="selectDisease" placeholder="请输入疾病中文名称" style="width:280px">
              <Option v-for="(item, index) in diseaseData" :value="item.DiseaseName_cn" :key="index">{{ item.DiseaseName_cn }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="Response Type" prop="ResponseType">
            <Select v-model="addForm.ResponseType" style="width:280px" placeholder="请选择应答类型">
              <Option value="1">1</Option>
              <Option value="2">2</Option>
              <Option value="3">3</Option>
            </Select>
          </FormItem>
          <FormItem label="治疗疗法名称" prop="TherapyName">
            <AutoComplete v-model="addForm.TherapyName" @on-search="searchTherapyName" @on-select="selectTherapyName" placeholder="请输入治疗疗法" style="width:280px">
              <Option v-for="(item, index) in therapyNameData" :value="item.TherapyName" :key="index">{{ item.TherapyName }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="证据等级" prop="EvidenceType">
            <Select v-model="addForm.EvidenceType" style="width:280px" placeholder="请选择证据等级">
              <Option value="1">1</Option>
              <Option value="2">2</Option>
              <Option value="3">3</Option>
            </Select>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      let search = location.search.split("&")
      this.clinicalTrialID = search[0].split('=')[1]
      //自动填充临床编号
      this.addForm.NCTID = search[1].split('=')[1]
      this.$http.all([
         this.$http.post("/php/targetedtherapy/getMolProfile.php"),
         this.$http.post("/php/chemotherapymodal/getDiseaseNum.php",qs.stringify({DiseaseID:'1',DiseaseName_cn:''})),
         this.$http.post("/php/targetedtherapy/getTargetedTherapy.php")
      ]).then(this.$http.spread((profile,disease,therapyName)=>{
        // console.log(profile.data);
        // console.log(disease.data);
        // console.log(therapyName.data);
        this.molProfileArr = profile.data
        this.molProfileData = this.molProfileArr
        this.diseaseArr = disease.data
        this.diseaseData = this.diseaseArr
        this.therapyNameArr = therapyName.data
        this.therapyNameData = this.therapyNameArr
      }))
    },
    data() {
      return {
        clinicalTrialID:'',
        nctID:'',
        molProfileData:[],
        molProfileArr:[],
        diseaseData:[],
        diseaseArr:[],
        therapyNameArr:[],
        therapyNameData:[],
        addForm: {
          NCTID:'',
          TargetTreatmentMolProfileID:'',
          MolProfileName:'',
          DiseaseID:'',
          DiseaseName_cn:'',
          ResponseType:'',
          TargetTreatmentTherapyID:'',
          TherapyName:'',
          EvidenceType:''
        },
        rule:{
          ClinicalTrialID:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ],
          MolProfileName:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ],
          DiseaseName_cn:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ],
          ResponseType:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ],
          TherapyName:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ],
          EvidenceType:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      searchProfile(value){
        this.$http.post("/php/targetedtherapy/getMolProfile.php",qs.stringify({MolProfileName:value})).then(response=>{
          // console.log(response.data);
          this.molProfileArr = response.data
          this.molProfileData = this.molProfileArr.filter(item=>{
            return item.MolProfileName.toUpperCase().indexOf(value.toUpperCase()) !== -1
          })
        })
      },
      selectProfile(value){
        // console.log(value);
        this.addForm.MolProfileName = value;
        this.molProfileData = this.molProfileArr.filter(item=>{
          return item.MolProfileName === value
        })
        this.addForm.TargetTreatmentMolProfileID = this.molProfileData[0].TargetTreatmentMolProfileID;
      },
      searchDisease(value){
        this.$http.post("/php/chemotherapymodal/getDiseaseNum.php",qs.stringify({DiseaseID:'',DiseaseName_cn:value})).then(response=>{
          this.diseaseArr = response.data
          this.diseaseData = this.diseaseArr.filter(item=>{
            return item.DiseaseName_cn.toUpperCase().indexOf(value.toUpperCase()) !== -1
          })
        })
      },
      selectDisease(value){
        // console.log(value);
        this.addForm.DiseaseName_cn = value;
        this.diseaseData = this.diseaseArr.filter(item=>{
          return item.DiseaseName_cn === value
        })
        this.addForm.DiseaseID = this.diseaseData[0].DiseaseID;
      },
      searchTherapyName(value){
        this.$http.post("/php/targetedtherapy/getTargetedTherapy.php",qs.stringify({TherapyName:value})).then(response=>{
          this.therapyNameArr = response.data
          this.therapyNameData = this.therapyNameArr.filter(item=>{
            return item.TherapyName.toUpperCase().indexOf(value.toUpperCase()) !== -1
          })
        })
      },
      selectTherapyName(value){
        this.addForm.TherapyName = value;
        this.therapyNameData = this.therapyNameArr.filter(item=>{
          return item.TherapyName === value
        })
        this.addForm.TargetTreatmentTherapyID = this.therapyNameData[0].TargetTreatmentTherapyID;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            //判断用户是从下拉框中选择，还是自己手动输入，如果是手动输入需判断输入的内容是否存在
            this.molProfileData = this.molProfileArr.filter(item=>{
              return item.MolProfileName===this.addForm.MolProfileName
            })
            if(this.molProfileData.length===0){
              this.$Message.error('分子谱名称不存在，请重新输入');
              return false;
            }
            this.diseaseData = this.diseaseArr.filter(item=>{
              return item.DiseaseName_cn===this.addForm.DiseaseName_cn
            })
            if(this.diseaseData.length===0){
              this.$Message.error('疾病中文名称不存在，请重新输入');
              return false;
            }
            this.therapyNameData = this.therapyNameArr.filter(item=>{
              return item.TherapyName===this.addForm.TherapyName
            })
            if(this.therapyNameData.length===0){
              this.$Message.error('治疗疗法不存在，请重新输入');
              return false;
            }
            let obj = {};
            for(var key in this.addForm){
              obj[key]=this.addForm[key].trim()
            }
            obj.ClinicalTrialID = this.clinicalTrialID
            this.$http.post("/php/targetedtherapy/add.php",qs.stringify(obj)).then(response=>{
              // console.log(response.data);
              if(response.data==="wrong"){
                this.$Message.error('返回' + response.data);
                return false;
              }
              if(response.data){
                this.$Message.success("添加成功！即将跳转页面...")
                setTimeout(()=>{
                  this.$router.push('/targetedtherapymodal/targetedtherapy')
                },1000)
              }else{
                this.$Message.error("添加失败，请检查输入内容")
              }
            })
          } else {
            this.$Message.error('Fail!');
          }
        })
      },
      backList(){
        this.$router.push("/targetedtherapymodal/targetedtherapy")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;
  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


