<template>
  <div class="relatedContainer">
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新靶向药物</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新靶向药物</p>
        <Form ref="addForm" :model="addForm" :rules='rule' label-position="right" :label-width="120">
          <FormItem label="药物名称（英文）" prop="DrugName_en">
            <AutoComplete v-model="addForm.DrugName_en" autofocus :disabled="step1" @on-search="searchDrug_en" @on-select="selectDrug_en" placeholder="请选择药物名称（英文）" style="width:280px">
              <Option v-for="(item, index) in drugData" :value="item.DrugName_en" :key="index">{{ item.DrugName_en }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="药物名称（中文）">
            <AutoComplete v-model="addForm.DrugName_cn" :disabled="step2" @on-search="searchDrug_cn" @on-select="selectDrug_cn" placeholder="请输入药物名称（中文）" style="width:280px">
              <Option v-for="(item, index) in drugData" :value="item.DrugName_cn" :key="index">{{ item.DrugName_cn }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="药物类别">
            <Input v-model="addForm.DrugClassName_en"  :disabled="step3" placeholder="请输入药物类别" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      this.$http.post("/php/targetedtherapy2/getDrug.php").then(response=>{
        // console.log(response.data);
        this.drugArr = response.data.list;
        this.drugData = response.data.list;
      })
    },
    data() {
      return {
        therapy2:location.search.split("=")[1],
        currentPath:location.pathname.split('/')[3],
        drugArr:'',
        drugData:'',
        step1:false,
        step2:false,
        step3:true,
        addForm: {
          DrugID:'',
          DrugName_en:'',
          DrugName_cn:'',
          DrugClassName_en:''
        },
        rule:{
          DrugName_en:[
            {
              required: true, message: '不能为空，请选择', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      searchDrug_en(value){
        this.$http.post("/php/targetedtherapy2/getDrug.php",qs.stringify({
          DrugName_en:value
        })).then(response=>{
          this.drugArr = response.data.list;
          this.drugData = response.data.list;
          // console.log(this.drugData);
        })
      },
      selectDrug_en(value){
        this.addForm.DrugName_en = value;
        this.drugData = this.drugArr.filter(item=>{
          return item.DrugName_en === value
        })
        // console.log(this.drugData);
        this.addForm.DrugID = this.drugData[0].DrugID;
        this.addForm.DrugName_cn = this.drugData[0].DrugName_cn;
        this.addForm.DrugClassName_en = this.drugData[0].DrugClassName_en;
      },
      searchDrug_cn(value){
        this.$http.post("/php/targetedtherapy2/getDrug.php",qs.stringify({
          DrugName_cn:value
        })).then(response=>{
          this.drugArr = response.data.list;
          this.drugData = response.data.list;
          // console.log(this.drugData);
        })
      },
      selectDrug_cn(value){
        this.addForm.DrugName_cn = value;
        this.drugData = this.drugArr.filter(item=>{
          return item.DrugName_cn === value
        })
        // console.log(this.drugData);
        this.addForm.DrugID = this.drugData[0].DrugID;
        this.addForm.DrugName_en = this.drugData[0].DrugName_en;
        this.addForm.DrugClassName_en = this.drugData[0].DrugClassName_en;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(this.drugData[0].DrugName_en===this.addForm.DrugName_en){
              this.$http.post("/php/targetedtherapy2/addTherapy.php",qs.stringify({
                TargetTreatmentTherapyID:this.therapy2,
                DrugID:this.addForm.DrugID
              })).then(response=>{
                // console.log(response.data);
                if(response.data==="wrong"){
                  this.$Message.error("该药物已被添加，请重新输入")
                  return false;
                }
                if(response.data){
                  this.$Message.success("添加成功！即将跳转页面...")
                  setTimeout(()=>{
                    this.$router.push('/targetedtherapymodal/browtargetedtherapy2detail/druglist?therapy2=' + this.therapy2)
                  },1000)
                }else{
                  this.$Message.error("添加失败，请检查输入内容")
                }
              })
            }else{
              this.$Message.error("药物英文名称输入错误，请检查")
            }
          }
        })
        
      },
      backList() {
        this.$router.push('/targetedtherapymodal/browtargetedtherapy2detail/druglist?therapy2=' + this.therapy2)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.relatedContainer{
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


