<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/targetedtherapy">靶向治疗</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='browtargetedtherapydetail'">查看</BreadcrumbItem>
      <BreadcrumbItem v-if="currentPath==='edittargetedtherapydetail'">编辑</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="deleteContainer">
        <Button type="warning">删除</Button>
      </div>
      <div class="editContainer">
        <Form ref="editForm" :model="editForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="创建时间">
            <Input v-model="editForm.CreateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="最新更新时间">
            <Input v-model="editForm.LatestUpdateDate" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="临床实验编号" prop="NCTID">
            <Input v-model="editForm.NCTID" disabled style="width: 280px"></Input>
          </FormItem>
          <FormItem label="分子谱名称" prop="MolProfileName">
            <AutoComplete v-model="editForm.MolProfileName" :disabled="currentPath==='browtargetedtherapydetail'" autofocus @on-search="searchProfile" @on-select="selectProfile" placeholder="请输入分子谱名称" style="width:280px">
              <Option v-for="(item, index) in molProfileData" :value="item.MolProfileName" :key="index">{{ item.MolProfileName }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="疾病名称" prop="DiseaseName_cn">
            <AutoComplete v-model="editForm.DiseaseName_cn" :disabled="currentPath==='browtargetedtherapydetail'" @on-search="searchDisease" @on-select="selectDisease" placeholder="请输入疾病中文名称" style="width:280px">
              <Option v-for="(item, index) in diseaseData" :value="item.DiseaseName_cn" :key="index">{{ item.DiseaseName_cn }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="Response Type" prop="ResponseType">
            <Select v-model="editForm.ResponseType" :disabled="currentPath==='browtargetedtherapydetail'" style="width:280px">
              <Option value="1">1</Option>
              <Option value="2">2</Option>
              <Option value="3">3</Option>
            </Select>
          </FormItem>
          <FormItem label="治疗疗法名称" prop="TherapyName">
            <AutoComplete v-model="editForm.TherapyName" :disabled="currentPath==='browtargetedtherapydetail'" @on-search="searchTherapyName" @on-select="selectTherapyName" placeholder="请输入治疗疗法" style="width:280px">
              <Option v-for="(item, index) in therapyNameData" :value="item.TherapyName" :key="index">{{ item.TherapyName }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="证据等级" prop="EvidenceType">
            <Select v-model="editForm.EvidenceType" :disabled="currentPath==='browtargetedtherapydetail'" style="width:280px">
              <Option value="1">1</Option>
              <Option value="2">2</Option>
              <Option value="3">3</Option>
            </Select>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="editSubmit" v-if="currentPath==='browtargetedtherapydetail'">编辑</Button>
            <Button type="primary" @click="handleSubmit('editForm')" v-if="currentPath==='edittargetedtherapydetail'">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      // console.log(this.targetTreatmentID);
      this.$http.post("/php/targetedtherapy/edit.php",qs.stringify({
        TargetTreatmentID:this.targetTreatmentID
      })).then(response=>{
        // console.log(response.data);
        this.editForm = response.data.list[0]
        //不可直接复制，引用数据类型只是赋值的是内存地址
        for(var key in response.data.list[0]){
          this.originForm[key]=response.data.list[0][key]
        }
      })
      this.$http.all([
         this.$http.post("/php/targetedtherapy/getMolProfile.php",qs.stringify({MolProfileName:this.editForm.MolProfileName})),
         this.$http.post("/php/chemotherapymodal/getDiseaseNum.php",qs.stringify({DiseaseID:'',DiseaseName_cn:this.editForm.DiseaseName_cn})),
         this.$http.post("/php/targetedtherapy/getTargetedTherapy.php",qs.stringify({TherapyName:this.editForm.TherapyName}))
      ]).then(this.$http.spread((profile,disease,therapyName)=>{
        // console.log(profile.data);
        // console.log(disease.data);
        // console.log(therapyName.data);
        this.molProfileArr = profile.data
        this.molProfileData = this.molProfileArr
        this.diseaseArr = disease.data
        this.diseaseData = this.diseaseArr
        this.therapyNameArr = therapyName.data
        this.therapyNameData = this.therapyNameArr
      }))
    },
    data() {
      return {
        currentPath:location.pathname.split('/')[2],
        targetTreatmentID:location.search.split("=")[1],
        molProfileData:[],
        molProfileArr:[],
        diseaseData:[],
        diseaseArr:[],
        therapyNameArr:[],
        therapyNameData:[],
        originForm:{},
        editForm:{},
        rule:{
          NCTID:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ],
          MolProfileName:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ],
          DiseaseName_cn:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ],
          ResponseType:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ],
          TherapyName:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ],
          EvidenceType:[
            {
              required:true,message:'不能为空',trigger:'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      editSubmit(){
        this.currentPath = 'edittargetedtherapydetail'
      },
      searchProfile(value){
        this.$http.post("/php/targetedtherapy/getMolProfile.php",qs.stringify({MolProfileName:value})).then(response=>{
          // console.log(response.data);
          this.molProfileArr = response.data
          this.molProfileData = this.molProfileArr.filter(item=>{
            return item.MolProfileName.toUpperCase().indexOf(value.toUpperCase()) !== -1
          })
        })
      },
      selectProfile(value){
        // console.log(value);
        this.editForm.MolProfileName = value;
        this.molProfileData = this.molProfileArr.filter(item=>{
          return item.MolProfileName === value
        })
        this.editForm.TargetTreatmentMolProfileID = this.molProfileData[0].TargetTreatmentMolProfileID;
      },
      searchDisease(value){
        this.$http.post("/php/chemotherapymodal/getDiseaseNum.php",qs.stringify({DiseaseID:'',DiseaseName_cn:value})).then(response=>{
          this.diseaseArr = response.data
          this.diseaseData = this.diseaseArr.filter(item=>{
            return item.DiseaseName_cn.toUpperCase().indexOf(value.toUpperCase()) !== -1
          })
        })
      },
      selectDisease(value){
        // console.log(value);
        this.editForm.DiseaseName_cn = value;
        this.diseaseData = this.diseaseArr.filter(item=>{
          return item.DiseaseName_cn === value
        })
        this.editForm.DiseaseID = this.diseaseData[0].DiseaseID;
      },
      searchTherapyName(value){
        this.$http.post("/php/targetedtherapy/getTargetedTherapy.php",qs.stringify({TherapyName:value})).then(response=>{
          this.therapyNameArr = response.data
          this.therapyNameData = this.therapyNameArr.filter(item=>{
            return item.TherapyName.toUpperCase().indexOf(value.toUpperCase()) !== -1
          })
        })
      },
      selectTherapyName(value){
        this.editForm.TherapyName = value;
        this.therapyNameData = this.therapyNameArr.filter(item=>{
          return item.TherapyName === value
        })
        this.editForm.TargetTreatmentTherapyID = this.therapyNameData[0].TargetTreatmentTherapyID;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            //判断用户是从下拉框中选择，还是自己手动输入，如果是手动输入需判断输入的内容是否存在
            this.molProfileData = this.molProfileArr.filter(item=>{
              return item.MolProfileName===this.editForm.MolProfileName
            })
            if(this.molProfileData.length===0){
              //判断用户是否修改了数据，因为自动匹配的数组中是不含有已经注册过的数据的
              if(this.editForm.MolProfileName.trim() !== this.originForm.MolProfileName.trim()){
                this.$Message.error('分子谱名称不存在，请重新输入');
                return false;
              }
            }
            this.diseaseData = this.diseaseArr.filter(item=>{
              return item.DiseaseName_cn===this.editForm.DiseaseName_cn
            })
            if(this.diseaseData.length===0){
              if(this.editForm.DiseaseName_cn.trim() !== this.originForm.DiseaseName_cn.trim()){
                this.$Message.error('疾病中文名称不存在，请重新输入');
                return false;
              }
            }
            this.therapyNameData = this.therapyNameArr.filter(item=>{
              return item.TherapyName===this.editForm.TherapyName
            })
            if(this.therapyNameData.length===0){
              if(this.editForm.TherapyName.trim() !== this.originForm.TherapyName.trim()){
                this.$Message.error('治疗疗法不存在，请重新输入');
                return false;
              }
            }
            let obj = {};
            for(var key in this.editForm){
              obj[key]=this.editForm[key].trim()
            }
            // console.log(obj)
            this.$http.post("/php/targetedtherapy/update.php",qs.stringify(obj)).then(response=>{
              // console.log(response.data);
              if(response.data==="wrong"){
                this.$Message.error("该靶向治疗信息已存在！请检查输入内容")
                return false;
              }
              if(response.data){
                this.$Message.success("保存成功！即将跳转页面...")
                setTimeout(()=>{
                  this.$router.push('/targetedtherapymodal/targetedtherapy')
                },1000)
              }else{
                this.$Message.error("保存失败！请检查输入内容")
              }
            })
          } else {
            this.$Message.error('Fail!');
          }
        })
      },
      backList(){
        this.$router.push("/targetedtherapymodal/targetedtherapy")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .deleteContainer,
  .editContainer{
    padding:5px 10px;
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .deleteContainer{
    text-align:right;
  }
}
</style>


