<template>
  <div class="relatedContainer">
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p>批量添加新的变异</p>
        <Button type="primary">下载模板</Button>
        <Button>批量导入</Button>
      </div>
      <div class="singleAddContainer">
        <p>添加单个新的变异（请按顺序依次填写并选择）</p>
        <Form ref="addForm" :model="addForm" :rules='rule' label-position="right" :label-width="120">
          <FormItem label="Gene Symbol" prop="Symbol">
            <AutoComplete v-model="addForm.Symbol" :disabled="step1" @on-search="searchSymbol" @on-select="selectSymbol" placeholder="请选择Gene Symbol" style="width:280px">
              <Option v-for="(item, index) in geneSymbolData" :value="item" :key="index">{{ item }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="HGVSp" prop="HGVSp">
            <AutoComplete v-model="addForm.HGVSp" :disabled="step2" @on-search="searchhgvsp" @on-select="selecthgvsp" placeholder="请选择HGVSp" style="width:280px">
              <Option v-for="(item, index) in hgvspData" :value="item.HGVSp" :key="index">{{ item.HGVSp }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem label="HGVSc" prop="HGVSc">
            <AutoComplete v-model="addForm.HGVSc" :disabled="step3" @on-search="searchhgvsc" @on-select="selecthgvsc" placeholder="请选择HGVSc" style="width:280px">
              <Option v-for="(item, index) in hgvscData" :value="item.HGVSc" :key="index">{{ item.HGVSc }}</Option>
            </AutoComplete>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')">保存</Button>
            <Button @click="backList">返回</Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      this.$http.post("/php/gsymbol/index.php").then(response=>{
        // console.log(response.data);
        this.geneSymbolArr = response.data;
        this.geneSymbolData = this.geneSymbolArr;
      })
    },
    data() {
      return {
        molProfileID:location.search.split("=")[1],
        step1:false,
        step2:true,
        step3:true,
        geneSymbolArr:[],
        geneSymbolData:[],
        hgvspArr:[],
        hgvspData:[],
        hgvscArr:[],
        hgvscData:[],
        addForm: {
          Symbol:'',
          HGVSp:'',
          HGVSc:'',
          VariantID:''
        },
        rule:{
          Symbol:[
            {
              required: true, message: '请选择Gene Symbol', trigger: 'blur,change'
            }
          ],
          HGVSp:[
            {
              required: true, message: '请选择Gene Symbol', trigger: 'blur,change'
            }
          ],
          HGVSc:[
            {
              required: true, message: '请选择Gene Symbol', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      searchSymbol(value){
        this.geneSymbolData = this.geneSymbolArr.filter(item=>{
          return item.toUpperCase().indexOf(value.toUpperCase()) !== -1
        })
      },
      selectSymbol(value){
        // console.log(value);
        this.addForm.Symbol = value;
        this.$http.post("/php/targetedtherapymodal/getHgvsp.php",qs.stringify({
          Symbol:value
        })).then(response=>{
          // console.log(response.data);
          this.hgvspArr = response.data.list;
          this.step1 = true;
          this.step2 = false;
          this.step3 = true;
          this.addForm.HGVSp = 'p.'
        })
      },
      searchhgvsp(value){
        this.hgvspData = this.hgvspArr.filter(item=>{
          return item.HGVSp.indexOf(value) !== -1
        })
      },
      selecthgvsp(value){
        this.addForm.HGVSp = value;    
        this.hgvscArr = this.hgvspData.filter(item=>{
          return item.HGVSp===value
        })
        this.step1 = true;
        this.step2 = true;
        this.step3 = false;
        this.addForm.HGVSc = 'c.'
      },
      searchhgvsc(value){
        this.hgvscData = this.hgvscArr.filter(item=>{
          return item.HGVSc.indexOf(value) !== -1
        })
      },
      selecthgvsc(value){
        this.addForm.HGVSc = value;
        this.hgvscData = this.hgvscArr.filter(item=>{
          return item.HGVSc===value;
        })
        this.addForm.VariantID = this.hgvscData[0].VariantID;
        this.step1 = true;
        this.step2 = true;
        this.step3 = true;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$http.post("/php/targetedtherapymodal/addMolProfile.php",qs.stringify({
              TargetTreatmentMolProfileID:this.molProfileID,
              VariantID:this.addForm.VariantID
            })).then(response=>{
              console.log(response.data);
              if(response.data==='wrong'){
                this.$Message.warning('此变异在该分子谱中已存在，请重新输入');
                return false;
              }
              if(response.data){
                this.$Message.success('添加成功！页面正在跳转...');
                setTimeout(()=>{
                  this.$router.push("/targetedtherapymodal/browvariantprofiledetail/variantlist?molprofileid=" + this.molProfileID)
                },1000)
              }else{
                this.$Message.error('添加失败！请检查输入内容');
              }
            })
          }
        })
        
      },
      backList() {
        this.$router.push("/targetedtherapymodal/browvariantprofiledetail/variantlist?molprofileid=" + this.molProfileID)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.relatedContainer{
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


