<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem>药物管理</BreadcrumbItem>
      <BreadcrumbItem>药物信息</BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="searchContainer">
        <Form :inline="true" ref="searchForm" :model="searchForm" label-position="right" :label-width="120">
          <FormItem label="药物编号">
            <Input v-model="searchForm.DrugID" placeholder="请输入药物编号"></Input>
          </FormItem>
          <FormItem label="药物英文名称">
            <Input v-model="searchForm.DrugName_en" placeholder="请输入药物英文名称"></Input>
          </FormItem>
          <FormItem label="药物中文名称">
            <Input v-model="searchForm.DrugName_cn" placeholder="请输入药物中文名称"></Input>
          </FormItem>
          <FormItem label="FDA药物状态">
            <Input v-model="searchForm.FDAStatus" placeholder="请输入FDA药物状态"></Input>
          </FormItem>
          <FormItem label="CFDA药物状态">
            <Input v-model="searchForm.CFDAStatus" placeholder="请输入CFDA药物状态"></Input>
          </FormItem>
          <FormItem label="药物类别">
            <Input v-model="searchForm.DrugClassName_en" placeholder="请输入药物类别"></Input>
            <!--<Select v-model="searchForm.DrugClassName_en" style="width:162px">
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>-->
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" icon="android-search" @click="handleSubmit('searchForm')">搜索</Button>
          </FormItem>
        </Form>
      </div>
      <div class="addContainer">
        <Button type="warning" @click="addNewDrug" size="large" icon="android-add">添加新药物</Button>
      </div>
      <div class="chromoListcontainer">
        <Table stripe border :loading="loading" ref="selection" :columns="tableHead" :data="tableData" @on-selection-change="handleSelectionChange"></Table>
        <div class="batchContainer">
          <Button type="text" size="large" class="batchBtn" @click="batchRemove">移除选中的{{multipleSelection.length}}个条目</Button>
        </div>
        <div class="pageContainer">
          <Page show-total show-sizer :total="totalNum" :current="pageNum" :page-size="pageSize" show-elevator placement="top" size="small" @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
        </div>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
      this.$http.post("/php/drug/index.php").then(response=>{
        // console.log(response.data);
        this.totalNum = response.data.num;
        this.tableData = response.data.data;
        response.data.data.forEach(item=>{
          if(item.DrugClassName_en.length!==0){
            let str = "";
            item.DrugClassName_en.forEach(value=>{
              str = (str + ";" + value.toString()).substring(1)
            })
            item.DrugClassName_en = str;
          }else{
            item.DrugClassName_en=""
          }
        })
        this.loading = false;
      })
    },
    data() {
      return {
        searchForm:{
          DrugID:'',
          DrugName_en:'',
          DrugName_cn:'',
          FDAStatus:'',
          CFDAStatus:'',
          DrugClassName_en:''
        },
        loading:true,
        tableHead:[
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title:"药物编号",
            key:'DrugID',
            align: 'center'
          },
          {
            title:"药物英文名称",
            key:'DrugName_en',
            align: 'center'
          },
          {
            title:"药物中文名称",
            key:'DrugName_cn',
            align: 'center'
          },
          {
            title:"FDA药物状态",
            key:'FDAStatus',
            align: 'center'
          },
          {
            title:"CFDA药物状态",
            key:'CFDAStatus',
            align: 'center'
          },
          {
            title:"药物类别",
            key:'DrugClassName_en',
            align: 'center'
          },
          {
            title:"最后修改时间",
            key:'LatestUpdateDate',
            align: 'center'
          },
          {
            title:"操作",
            key:'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'document'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.row.DrugID)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: "edit"
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.row.DrugID)
                    }
                  }
                },''),
                h('Button', {
                  props: {
                    type: 'text',
                    size: 'large',
                    icon: 'android-delete'
                  },
                  style: {
                    padding: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.row.DrugID)
                    }
                  }
                }, '')
              ]);
            }
          },
        ],
        tableData:[],
        totalNum:0,
        multipleSelection:[],
        pageNum:1,
        pageSize:10
      }
    },
    methods: {
      handleSubmit(name) {
        this.loading = true;
        this.pageSize = 10;
        this.pageNum = 1;
        let obj = {};
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim();
        }
        this.$http.post("/php/drug/index.php",qs.stringify(obj)).then(response=>{
          // console.log(response.data);
          this.totalNum = response.data.num;
          this.tableData = response.data.data;
          response.data.data.forEach(item=>{
            if(item.DrugClassName_en.length!==0){
              let str = "";
              item.DrugClassName_en.forEach(value=>{
                str = (str + "/" + value.toString()).substring(1)
              })
              item.DrugClassName_en = str;
            }else{
              item.DrugClassName_en=""
            }
          })
          this.loading = false;
        })
      },
      addNewDrug(){
        this.$router.push("/basicdataModal/addnewdrug")
      },
      show (id) {
        console.log(id);
        this.$router.push("/basicdataModal/browdrugdetail?drugid=" + id)
      },
      edit (id) {
        console.log(id);
        this.$router.push("/basicdataModal/editdrugdetail?drugid=" + id)
      },
      remove(id){
        // console.log(id);
        this.loading = true;
        this.$http.post("/php/drug/del.php",qs.stringify({
          DrugID:[id]
        })).then(response=>{
          // console.log(response.data);
          if(response.data==="wrong"){
            this.$Message.error("该药物已被引用，无法删除！")
            this.loading = false;
            return false;
          }
          if(response.data){
            this.$Message.success("删除成功！正在刷新页面...")
            this.handlePage(this.pageNum)
          }else{
            this.$Message.error("删除失败！")
            tihs.loading = false;
          }
        })
      },
      batchRemove(){
        this.loading = true;
        let IDS = [];
        this.multipleSelection.forEach(item=>{
          IDS.push(item.DrugID)
        })
        this.$http.post("/php/drug/del.php",qs.stringify({
          DrugID:IDS
        })).then(response=>{
          // console.log(response.data);
          if(response.data==="wrong"){
            this.$Message.error("所选药物中，有的药物已被引用，无法完成批量删除操作！")
            this.loading = false;
            return false;
          }
          if(response.data){
            this.$Message.success("批量删除成功！正在刷新页面...")
            this.handlePage(this.pageNum)
          }else{
            this.$Message.error("批量删除失败！")
            tihs.loading = false;
          }
        })    
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handlePage(val){
        this.loading = true;
        this.pageNum = val;
        let obj = {};
        for(var key in this.searchForm){
          obj[key] = this.searchForm[key].trim();
        }
        obj.pageNum = this.pageNum;
        obj.pageSize = this.pageSize;
        this.$http.post("/php/drug/index.php",qs.stringify(obj)).then(response=>{
          // console.log(response.data);
          this.totalNum = response.data.num;
          this.tableData = response.data.data;
          response.data.data.forEach(item=>{
            if(item.DrugClassName_en.length!==0){
              let str = "";
              item.DrugClassName_en.forEach(value=>{
                str = (str + "/" + value.toString()).substring(1)
              })
              item.DrugClassName_en = str;
            }else{
              item.DrugClassName_en=""
            }
          })
          this.loading = false;
        })
      },
      handlePageSize(val){
        this.loading = true;
        this.pageNum = 1;
        this.pageSize = val;
        this.handlePage(this.pageNum);
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    min-height: 400px;
    padding: 10px;
    background-color: #fff;
  }
  .searchContainer{
    padding:5px 0;
    border:@bgc 1px solid;
    .ivu-form-item{
      margin:5px 0;
    }
  }
  .addContainer{
    padding:10px;

  }
  .chromoListcontainer{
    .ivu-table-body{
      overflow:hidden;
      .ivu-table-cell{
        padding:0px !important;
      }
    }
    .batchContainer{
      padding:5px;
      .batchBtn{
        color:@themeColor;
        &:hover{
          color:@warningColor;
        }
      }
    }
    .pageContainer{
      text-align:right;
      padding: 10px;
    }
  }
  
}
</style>


